import { useEffect, useState } from "react";
import { Button, Col, Form, Modal, Row } from "react-bootstrap";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { Translate } from "../../../Enums/Tranlate";
import Select from "react-select";
import "../style.scss";

const AddSubjectsModal = ({
	addModal,
	setAddModal,
	item,
	type,
	setShouldUpdate,
	Service,
}) => {
	const [formData, setFormData] = useState({
		subjects: "",
		age_group: "",
		category: "",
	});
	const [isAdd, setIsAdd] = useState(false);
	const [loading, setLoading] = useState(false);
	const lang = useSelector((state) => state.auth.lang);

	useEffect(() => {
		if (Object.keys(item)?.length === 0) {
			setIsAdd(true);
		} else {
			setIsAdd(false);
			setFormData({
				id: item?.id,
				name: item?.name,
			});
		}
	}, [item]);

	const submit = (e) => {
		e.preventDefault();
		let data = {};

		setLoading(true);
		if (isAdd) {
			Service.create(data)
				?.then((res) => {
					if (res && res?.status === 201) {
						toast.success(`Subject Added Successfully`);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		} else {
			Service.update(formData?.id, data)
				?.then((res) => {
					if (res && res?.status === 200) {
						toast.success(`Subject Updated Successfully`);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	return (
		<Modal
			className={
				lang === "en" ? "en fade addDepartment" : "ar fade addDepartment"
			}
			style={{ textAlign: lang === "en" ? "left" : "right" }}
			show={addModal}
			onHide={() => {
				setAddModal();
			}}>
			<AvForm className="form-horizontal" onValidSubmit={submit}>
				<Modal.Body>
					<Row>
						<Col md={12} className="mb-2">
							<div className="d-flex justify-content-between">
								<p className="mb-0">
									{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
									{type === "categories"
										? Translate[lang].category
										: Translate[lang].subject}
								</p>
								<Button
									variant=""
									className="close"
									style={{
										right: lang === "en" ? "0" : "auto",
										left: lang === "ar" ? "0" : "auto",
									}}
									onClick={() => {
										setAddModal();
									}}>
									<span style={{ fontSize: "22px" }}>&times;</span>
								</Button>
							</div>
						</Col>
						{type === "categories" && (
							<Col md={12}>
								<AvField
									label={`${Translate[lang]?.category}`}
									name="category"
									type="text"
									value={formData?.category}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.category}`}
									onChange={(e) =>
										setFormData({ ...formData, category: e.target.value })
									}
								/>
							</Col>
						)}
						<Col md={12}>
							<label className="text-label">
								{Translate[lang]?.subjects}{" "}
								<span>({Translate[lang]?.multi_select})</span>
							</label>
							<Select
								value={formData?.subjects}
								name="subjects"
								placeholder={Translate[lang]?.select}
								options={[]}
								onChange={(e) => setFormData({ ...formData, subjects: e })}
							/>
						</Col>
						{type === "subjects" && (
							<Col md={12} className="mt-3">
								<label className="text-label">
									{Translate[lang]?.age_group}
								</label>
								<Select
									value={formData?.age_group}
									name="age_group"
									placeholder={Translate[lang]?.select}
									options={[]}
									onChange={(e) => setFormData({ ...formData, age_group: e })}
								/>
							</Col>
						)}
					</Row>
				</Modal.Body>
				<Modal.Footer>
					{/* <Button onClick={setAddModal} variant="danger light">
            {Translate[lang]?.close}
            </Button> */}
					<Button
						variant="primary"
						type="submit"
						className="w-100"
						disabled={loading}>
						{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
						{type === "categories"
							? Translate[lang].category
							: Translate[lang].subject}
					</Button>
				</Modal.Footer>
			</AvForm>
		</Modal>
	);
};

export default AddSubjectsModal;
