import { useState } from "react";
import { Button, Card, Table } from "react-bootstrap";
import { useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import Loader from "../../common/Loader";
import NoData from "../../common/NoData";
import Pagination from "../../common/Pagination/Pagination";
import { Translate } from "../../Enums/Tranlate";
import CardItem from "./CardItem";
import GalleryService from "../../../services/GalleryService";

const Gallery = () => {
  const [data, setData] = useState([]);
  const [hasData, setHasData] = useState(null);
  const [shouldUpdate, setShouldUpdate] = useState(false);
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();
  const galleryService = new GalleryService();
  const Auth = useSelector((state) => state.auth?.auth);
  const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
  const lang = useSelector((state) => state.auth?.lang);

  return (
    <div className="organization">
        <div className="mb-4">
        <div className="assets-div d-flex justify-content-between align-items-center">
          <div>
            <span style={{
              fontWeight: '600',
              textTransform: 'capitalize',
              color: '#000',
            }}>{Translate[lang].gallery}</span>
          </div>
          <div>
            {<Link to='/gallery/add' className='btn btn-primary h-75'>
              <i className="la la-plus mx-1"></i>
              {Translate[lang]?.add} {Translate[lang]?.gallery}
            </Link>}
          </div>
        </div >
      </div>
      <Card>
        <div className={`${hasData === 0 ? "text-center" : "table-style"}`}>
          {loading && (
            <div style={{ height: "300px" }}>
              <Loader />
            </div>
          )}
          {hasData === 1 && !loading && (
            <Table responsive>
              <thead>
              <tr>
                  <th>
                    <strong>{Translate[lang]?.title}</strong>
                  </th>
                  <th>
                    <strong>{Translate[lang]?.num_of_images}</strong>
                  </th>
                  <th>
                    <strong>{Translate[lang]?.banner_image}</strong>
                  </th>
                  <th className="text-center">
                    <strong>{Translate[lang]?.action}</strong>
                  </th>
                </tr>
              </thead>
              <tbody>
                {data?.map((item, index) => {
                    return (
                      <CardItem
                        key={index}
                        index={index}
                        item={item}
                        setShouldUpdate={setShouldUpdate}
                      />
                    );
                  })}
              </tbody>
            </Table>
          )}
          {hasData === 0 && <NoData />}
          <Pagination
            setData={setData}
            service={galleryService}
            shouldUpdate={shouldUpdate}
            setHasData={setHasData}
            setLoading={setLoading}
          />
        </div>
      </Card>
    </div>
  );
};
export default Gallery;
