import React, { useEffect, useState } from "react";
import { Button, Col, Row } from "react-bootstrap";
import { toast } from "react-toastify";
import { AvForm } from "availity-reactstrap-validation";
import { useSelector } from "react-redux";
import { Translate } from "../../Enums/Tranlate";
import "./style.scss";
import BaseService from "../../../services/BaseService";
import Loader from "../../common/Loader";
import { Editor } from "react-draft-wysiwyg";
import { EditorState, convertToRaw, ContentState } from "draft-js";
import draftToHtml from "draftjs-to-html";
import htmlToDraft from "html-to-draftjs";
import StaticPagesService from "../../../services/StaticPagesService";

const Static = () => {
	const initial = {
		title: EditorState.createEmpty(),
		description: EditorState.createEmpty(),
		image: "",
	};
	const [formData, setFormData] = useState(initial);
	const [loading, setLoading] = useState(false);
	const [submitLoading, setSubmitLoading] = useState(false);
	const [type, setType] = useState("terms-conditions");
	const lang = useSelector((state) => state.auth?.lang);
	const staticService = new StaticPagesService();

	useEffect(() => {
		staticService.getList(type).then((res) => {
			if (res?.status) {
				let response = res?.data?.data;
				setFormData({
					title: EditorState.createWithContent(
						ContentState.createFromBlockArray(
							htmlToDraft(response?.title?.replaceAll("&lt;", "<"))
						)
					),
					description: EditorState.createWithContent(
						ContentState.createFromBlockArray(
							htmlToDraft(response?.description?.replaceAll("&lt;", "<"))
						)
					),
					image: response?.image,
				});
			} else {
				setFormData(initial);
			}
		});
	}, [type]);

	const onSubmit = (e) => {
		e.preventDefault();

		if (!formData?.image || !formData?.title || !formData?.description) {
			if (!formData?.image) toast.error("Image Required");
			if (!formData?.title) toast.error("Title Required");
			if (!formData?.description) toast.error("Description Required");
			return;
		}
		let info = {
			title: draftToHtml(convertToRaw(formData?.title.getCurrentContent())),
			description: draftToHtml(
				convertToRaw(formData?.description.getCurrentContent())
			),
			image: formData?.image,
		};

		setSubmitLoading(true);
		staticService.create(type, info).then((response) => {
			if (response?.status === 200) {
				toast.success(response?.data?.message);
			}
			setSubmitLoading(false);
		});
	};

	const fileHandler = (e) => {
		let files = e.target.files;
		const filesData = Object.values(files);
		setLoading(true);
		if (filesData?.length) {
			new BaseService()
				.postUpload(filesData[0])
				.then((res) => {
					if (res?.status === 201) {
						setFormData({
							...formData,
							image: res?.data?.data?.uploadedFiles[0]?.url || "",
						});
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	return (
		<div className="department">
			<div className="tabs-div">
				<p
					onClick={() => setType("terms-conditions")}
					className={type === "terms-conditions" ? "activeP" : ""}>
					{Translate[lang].terms_conditions}
				</p>
				<p
					onClick={() => setType("privacy-policy")}
					className={type === "privacy-policy" ? "activeP" : ""}>
					{Translate[lang].privacy}
				</p>
			</div>
			<div className="mb-4">
				<span
					style={{
						fontWeight: "600",
						textTransform: "capitalize",
						color: "#000",
					}}>
					{Translate[lang][type]}
				</span>
			</div>
			<Row className="about">
				<Col md={10}>
					<AvForm onValidSubmit={onSubmit}>
						<div className="row">
							<div className="col-lg-12 col-sm-12 mb-3">
								<label>{Translate[lang].title}</label>
								{/* <textarea
									name="title"
									value={formData.title}
									placeholder={Translate[lang].title}
									onChange={(e) =>
										setFormData({ ...formData, title: e.target?.value })
									}
									className="w-100"
								/> */}
								<Editor
									editorState={formData.title}
									toolbarClassName="toolbarClassName"
									wrapperClassName="wrapperClassName"
									editorClassName="editorClassName"
									onEditorStateChange={(e) =>
										setFormData({ ...formData, title: e })
									}
								/>
								<span className="max">
									{Translate[lang].Max} 200 {Translate[lang].Word}
								</span>
							</div>
							<div className="col-lg-12 col-sm-12 mb-3">
								<label>{Translate[lang].description}</label>
								<Editor
									editorState={formData.description}
									toolbarClassName="toolbarClassName"
									wrapperClassName="wrapperClassName"
									editorClassName="editorClassName"
									onEditorStateChange={(e) =>
										setFormData({ ...formData, description: e })
									}
								/>
								<span className="max">
									{Translate[lang].Max} 2000 {Translate[lang].Word}
								</span>
							</div>
							<div className="col-lg-12 col-sm-12 mb-3">
								<div className="form-group w-100">
									<label className="m-0">{Translate[lang]?.image}</label>
									<div className="image-placeholder">
										<div className="avatar-edit">
											<input
												type="file"
												onChange={(e) => fileHandler(e)}
												id={`imageUpload1`}
											/>
											<label htmlFor={`imageUpload1`} name=""></label>
										</div>
										<div className="avatar-preview2 m-auto">
											<div id={`imagePreview`} className="images">
												<>
													<i className="la la-plus-circle" />
													<span style={{ fontSize: "18px", fontWeight: "600" }}>
														{Translate[lang]?.add} {Translate[lang]?.image}
													</span>
													<span style={{ fontSize: "12px" }}>
														{Translate[lang]?.max_size} 20{" "}
														{Translate[lang]?.mbs}
													</span>
												</>
												{loading && <Loader />}
											</div>
										</div>
									</div>
									{!!formData?.image && (
										<div className="about_image">
											<img src={formData?.image} alt="banner_image" />
											<a
												href={formData?.image}
												target="_blank"
												rel="noreferrer">
												<i className="la la-eye" />
											</a>
										</div>
									)}
								</div>
							</div>
							<div className="col-lg-6 col-sm-6 mt-4 mb-5">
								<Button
									variant="primary"
									type="submit"
									className="w-100 fs-14"
									disabled={submitLoading}>
									{Translate[lang]?.save}
								</Button>
							</div>
						</div>
					</AvForm>
				</Col>
			</Row>
		</div>
	);
};

export default Static;
