import { useEffect, useState } from "react";
import { Button, Col, Form, Modal, Row } from "react-bootstrap";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { Translate } from "../../../Enums/Tranlate";
import Select from "react-select";
import "../style.scss";
import OrganizationService from "../../../../services/OrganizationService";

const AddDepartmentsModal = ({
	addModal,
	setAddModal,
	item,
	assign,
	setShouldUpdate,
	type,
	Service,
}) => {
	const [formData, setFormData] = useState({
		name: "",
		department_manager: "",
		unit_manager: "",
		isActive: true,
	});
	const [isAdd, setIsAdd] = useState(false);
	const [options, setOptions] = useState([]);
	const [loading, setLoading] = useState(false);
	const lang = useSelector((state) => state.auth.lang);

	useEffect(() => {
		if (type === "departments") {
			new OrganizationService().getList().then((res) => {
				if (res?.status) {
					let data = res?.data?.data?.map((info) => {
						return {
							label: info?.legalName,
							value: info?.id,
						};
					});
					setOptions(data);
				}
			});
		}
	}, []);

	useEffect(() => {
		if (Object.keys(item)?.length === 0) {
			setIsAdd(true);
		} else {
			setIsAdd(false);
			let info = {
				id: item?.id,
				name: item?.name,
				isActive: item?.isActive,
			};
			setFormData(info);
		}
	}, [item]);

	const submit = (e) => {
		e.preventDefault();
		let data = {
			name: formData?.name,
			isActive: formData?.isActive,
		};
		if (type === "departments") data["organizationId"] = formData?.type?.value;

		setLoading(true);
		if (isAdd) {
			Service.create(data)
				?.then((res) => {
					if (res && res?.status === 201) {
						toast.success(res?.data?.message);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		} else {
			Service.update(formData?.id, data)
				?.then((res) => {
					if (res && res?.status === 200) {
						toast.success(res?.data?.message);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	return (
		<Modal
			className={
				lang === "en" ? "en fade addDepartment" : "ar fade addDepartment"
			}
			style={{ textAlign: lang === "en" ? "left" : "right" }}
			show={addModal}
			onHide={() => {
				setAddModal();
			}}>
			<AvForm className="form-horizontal" onValidSubmit={submit}>
				<Modal.Body>
					<Row>
						<Col md={12} className="mb-2">
							<div className="d-flex justify-content-between">
								<p className="mb-0"></p>
								<Button
									variant=""
									className="close"
									style={{
										right: lang === "en" ? "0" : "auto",
										left: lang === "ar" ? "0" : "auto",
									}}
									onClick={() => {
										setAddModal();
									}}>
									<span style={{ fontSize: "22px" }}>&times;</span>
								</Button>
							</div>
						</Col>
						<Col md={12} className="mb-4">
							<div className="d-flex justify-content-between align-items-center">
								<p
									className="mb-0"
									style={{
										fontWeight: "600",
										color: "#333",
										fontSize: "20px",
									}}>
									{assign
										? Translate[lang]?.assign_manager
										: isAdd
										? Translate[lang]?.add
										: Translate[lang]?.edit}{" "}
									{assign ? "" : Translate[lang]?.department}
								</p>
								{!assign && (
									<Form.Check
										type="switch"
										id="custom-switch"
										checked={formData?.isActive}
										onChange={(e) =>
											setFormData({ ...formData, isActive: e.target.checked })
										}
									/>
								)}
							</div>
						</Col>
						{!assign && (
							<Col md={12}>
								<AvField
									label={
										type === "departments"
											? Translate[lang]?.department_name
											: Translate[lang]?.unit_name
									}
									type="text"
									placeholder={
										type === "departments"
											? Translate[lang]?.department_name
											: Translate[lang]?.unit_name
									}
									bsSize="lg"
									name="name"
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									value={formData.name}
									onChange={(e) =>
										setFormData({
											...formData,
											name: e.target.value,
										})
									}
								/>
							</Col>
						)}
						{!assign && (
							<Col md={12}>
								<label className="text-label">
									{type === "departments"
										? Translate[lang]?.organization
										: Translate[lang]?.department}
								</label>
								<Select
									value={formData?.type}
									name="type"
									placeholder={Translate[lang]?.select}
									options={options}
									onChange={(e) => setFormData({ ...formData, type: e })}
								/>
							</Col>
						)}

						{assign && (
							<Col md={12}>
								<label className="text-label">
									{type === "units"
										? Translate[lang]?.unit_manager
										: Translate[lang]?.department_manager}
								</label>
								<Select
									value={formData?.department_manager}
									name="department_manager"
									placeholder={Translate[lang]?.select}
									options={[]}
									onChange={(e) =>
										setFormData({ ...formData, department_manager: e })
									}
								/>
							</Col>
						)}
					</Row>
				</Modal.Body>
				<Modal.Footer>
					{/* <Button onClick={setAddModal} variant="danger light">
            {Translate[lang]?.close}
            </Button> */}
					<Button
						variant="primary"
						type="submit"
						className="w-100"
						disabled={loading}>
						{assign
							? Translate[lang]?.assign_manager
							: isAdd
							? Translate[lang]?.add
							: Translate[lang]?.edit}{" "}
						{assign
							? ""
							: type === "departments"
							? Translate[lang]?.department
							: Translate[lang]?.unit}
					</Button>
				</Modal.Footer>
			</AvForm>
		</Modal>
	);
};

export default AddDepartmentsModal;
