import { useState } from "react";
import { Badge } from "react-bootstrap";
import { useSelector } from "react-redux";
import DeleteModal from "../../../common/DeleteModal";
import { Translate } from "../../../Enums/Tranlate";
import editIcon from "../../../../images/icons/edit.svg";
import trashIcon from "../../../../images/icons/delete.svg";
import viewIcon from "../../../../images/icons/eye.svg";
import { Link } from "react-router-dom";
import BranchesService from "../../../../services/BranchesService";

const CardItem = ({
	item,
	setItem,
	index,
	setAddModal,
	setAssign,
	setShouldUpdate,
}) => {
	const [deleteModal, setDeleteModal] = useState(false);
	const Auth = useSelector((state) => state.auth?.auth);
	const lang = useSelector((state) => state.auth?.lang);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const branchesService = new BranchesService();

	return (
		<tr key={index} className="text-center">
			<td>
				<Link
					to={`/branches/${item?.id}`}
					state={item}
					style={{
						color: "#0F50A6",
						fontWeight: "500",
					}}>
					{item?.name}
				</Link>
			</td>
			<td>{item?.branchId}</td>
			<td>{item?.branchNo}</td>
			<td>{item?.phoneNumber}</td>
			<td>{item?.email}</td>
			<td>
				{!item?.managerId ? (
					<button
						class="btn btn-outline-primary d-flex align-items-center"
						onClick={() => {
							setAssign(true);
							setAddModal(true);
							setItem(item);
						}}
						style={{ gap: "8px" }}>
						<i className="la la-plus" />
						{Translate[lang].assign_manager}
					</button>
				) : (
					"Mohanned"
				)}
			</td>
			<td>Fixed</td>
			<td>Idea</td>
			<td>
				<Badge
					className="text-capitalize"
					variant={`${item.isActive ? "success" : "danger"} light`}>
					{item?.isActive ? Translate[lang].active : Translate[lang].deactive}
				</Badge>
			</td>
			<td>
				<div className="d-flex" style={{ gap: "20px" }}>
					<Link
						to={`/branches/${item?.id}`}
						state={item}
						style={{
							color: "#0F50A6",
							fontWeight: "500",
						}}>
						<img src={viewIcon} alt="view" width={24} />
					</Link>
					<img
						src={editIcon}
						className="cursor-pointer"
						onClick={() => {
							setAssign(false);
							setItem(item);
							setAddModal(true);
						}}
						alt="edit"
						width={24}
					/>
					<img
						src={trashIcon}
						className="cursor-pointer"
						onClick={() => setDeleteModal(true)}
						alt="trash"
						width={24}
					/>
				</div>
			</td>
			{deleteModal && (
				<DeleteModal
					open={deleteModal}
					titleMsg={item?.name}
					deletedItem={item}
					modelService={branchesService}
					onCloseModal={setDeleteModal}
					setShouldUpdate={setShouldUpdate}
				/>
			)}
		</tr>
	);
};
export default CardItem;
