import { useEffect, useState } from "react";
import { Button, Col, Modal, Row } from "react-bootstrap";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import Select from "react-select";
import { Translate } from "../../../Enums/Tranlate";
import AllowanceService from "../../../../services/AllowanceService";

const AddAllowancesModal = ({
	addModal,
	setAddModal,
	item,
	setShouldUpdate,
}) => {
	const [formData, setFormData] = useState({
		name: "",
		fee: "",
	});
	const [isAdd, setIsAdd] = useState(false);
	const [loading, setLoading] = useState(false);
	const allowanceService = new AllowanceService();
	const lang = useSelector((state) => state.auth.lang);

	useEffect(() => {
		if (Object.keys(item)?.length === 0) {
			setIsAdd(true);
		} else {
			setIsAdd(false);
			setFormData({
				id: item?.id,
				name: item?.name,
				fee: item?.fee,
			});
		}
	}, [item]);

	const submit = (e) => {
		e.preventDefault();
		let data = {
			name: formData?.name,
			fee: formData?.fee,
		};

		setLoading(true);
		if (isAdd) {
			allowanceService
				.create(data)
				?.then((res) => {
					if (res && res?.status === 201) {
						toast.success("Allowance Added Successfully");
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		} else {
			allowanceService
				.update(formData?.id, data)
				?.then((res) => {
					if (res && res?.status === 200) {
						toast.success("Allowance Updated Successfully");
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	return (
		<Modal
			className={
				lang === "en" ? "en fade addDepartment" : "ar fade addDepartment"
			}
			style={{ textAlign: lang === "en" ? "left" : "right" }}
			show={addModal}
			onHide={() => {
				setAddModal();
			}}>
			<AvForm className="form-horizontal" onValidSubmit={submit}>
				<Modal.Header>
					<Modal.Title>
						{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
						{Translate[lang]?.allowances}
					</Modal.Title>
					<Button
						variant=""
						className="close"
						style={{
							right: lang === "en" ? "0" : "auto",
							left: lang === "ar" ? "0" : "auto",
						}}
						onClick={() => {
							setAddModal();
						}}>
						<span>&times;</span>
					</Button>
				</Modal.Header>
				<Modal.Body>
					<Row>
						<Col md={12} className="mb-3">
							<label className="text-label">{Translate[lang].employee}</label>
							<Select
								placeholder={Translate[lang]?.select}
								options={[]}
								value={formData.employee}
								isDisabled
								onChange={(e) => setFormData({ ...formData, employee: e })}
							/>
						</Col>
						<Col md={12} className="mb-3">
							<label className="text-label">{Translate[lang].type}</label>
							<Select
								placeholder={Translate[lang]?.select}
								options={[]}
								value={formData.allowance_type}
								onChange={(e) =>
									setFormData({ ...formData, allowance_type: e })
								}
							/>
						</Col>
						<Col md={12}>
							<AvField
								label={Translate[lang]?.fees}
								type="number"
								placeholder={Translate[lang]?.fees}
								bsSize="lg"
								name="fees"
								validate={{
									required: {
										value: true,
										errorMessage: Translate[lang].field_required,
									},
								}}
								value={formData.fee}
								onChange={(e) =>
									setFormData({ ...formData, fee: e.target.value })
								}
							/>
						</Col>
						<Col md={12} className="mb-3">
							<label className="text-label">
								{Translate[lang].payment_method}
							</label>
							<Select
								placeholder={Translate[lang]?.select}
								options={[]}
								value={formData.payment_method}
								disabled
								onChange={(e) =>
									setFormData({ ...formData, payment_method: e })
								}
							/>
						</Col>
						<Col md={12} className="mb-3">
							<AvField
								label={Translate[lang]?.assign_date}
								type="date"
								placeholder={Translate[lang]?.assign_date}
								bsSize="lg"
								name="assign_date"
								validate={{
									required: {
										value: true,
										errorMessage: Translate[lang].field_required,
									},
								}}
								value={formData.assign_date}
								onChange={(e) =>
									setFormData({ ...formData, assign_date: e.target.value })
								}
							/>
						</Col>
						<Col md={12}>
							<Button
								className="w-100"
								variant="primary"
								type="submit"
								disabled={loading}>
								{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
								{Translate[lang]?.allowances}
							</Button>
						</Col>
					</Row>
				</Modal.Body>
			</AvForm>
		</Modal>
	);
};

export default AddAllowancesModal;
