import React, { Fragment, useEffect } from "react";
import { useState } from "react";
import { Row, Col, Card, Table, Button } from "react-bootstrap";
import { useSelector } from "react-redux";
import CardItem from "./CardItem";
import "../style.scss";
import print from "../../../Enums/Print";
import EmployeesService from "../../../../services/EmployeesService";
import { Translate } from "../../../Enums/Tranlate";
import Pagination from "../../../common/Pagination/Pagination";
import NoData from "../../../common/NoData";
import Loader from "../../../common/Loader";
import { ExportExcel } from "../../../Enums/Export";
import { useNavigate } from "react-router-dom";

const ActiveEmployees = () => {
	const [data, setData] = useState([]);
	const [hasData, setHasData] = useState(null);
	const [loading, setLoading] = useState(false);
	const [shouldUpdate, setShouldUpdate] = useState(false);
	const lang = useSelector((state) => state.auth?.lang);
	const Auth = useSelector((state) => state.auth?.auth);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const employeesService = new EmployeesService();
	const navigate = useNavigate();

	const printProjects = () => {
		setLoading(true);
		let rows = [
			Translate[lang]?.name,
			Translate[lang]?.civil_id,
			Translate[lang]?.expiry_date,
			Translate[lang]?.passport_no,
			Translate[lang]?.passport_expiry_date,
			Translate[lang]?.job_title,
			Translate[lang]?.department,
			Translate[lang]?.personal_email,
			Translate[lang]?.company_email,
			Translate[lang]?.start_date,
			Translate[lang]?.assets,
		];
		if (isExist("view_salaries")) {
			rows.push(Translate[lang]?.salary);
		}
		employeesService
			.getList()
			.then((res) => {
				if (res?.status === 200) {
					if (res?.data?.data?.data?.length === 0) {
						setLoading(false);
						return;
					}
					print(
						Translate[lang]?.employees,
						rows,
						lang,
						res?.data?.data?.data.map((item) => {
							let info = {
								name: item.name,
								civil_id: item.civil_id,
								expiry_date: item.expiry_date || "",
								passport_no: item.passport_no || "",
								passport_expiry_date: item.passport_expiry_date || "",
								job_title: item.job_title,
								department: item.department?.name,
								personal_email: item?.personal_email,
								company_email: item?.company_email,
								start_date: item?.start_date?.split("T")[0],
								assets: item.employee_assets?.length,
							};
							if (isExist("view_salaries")) {
								info["salary"] = item.salary;
							}
							return info;
						})
					);
				}
				setLoading(false);
			})
			.catch(() => setLoading(false));
	};

	const exportToExcel = () => {
		employeesService.getList().then((res) => {
			if (res?.status === 200) {
				let response = res?.data?.data?.data;
				ExportExcel(response);
			}
		});
	};

	useEffect(() => {
		setLoading(true);
		employeesService.getList().then((res) => {
			if (res?.status) {
				setData(res?.data?.data);
				if (!!res?.data?.data?.length) {
					setHasData(1);
				} else {
					setHasData(0);
				}
			}
			setLoading(false);
		});
	}, [shouldUpdate]);

	return (
		<Fragment>
			<div className="mb-4 d-flex justify-content-between align-items-center">
				<div>
					<span
						style={{
							fontWeight: "600",
							textTransform: "capitalize",
							color: "#000",
						}}>
						{Translate[lang].employees}
					</span>
				</div>
				<div>
					{/* <Button 
              variant='outline-secondary' 
              className='mx-2 h-75'
              onClick={exportToExcel}
            >
              {Translate[lang].export}
            </Button> */}
					{/* <Button 
              variant='secondary' 
              className='mx-2 h-75'
              onClick={printProjects}
            >
              {Translate[lang].print}
            </Button> */}
					{
						<Button
							variant="primary"
							className="me-2 h-75"
							onClick={() => navigate("/employees/add-employee")}>
							<i className="la la-plus mx-1"></i>
							{Translate[lang]?.add} {Translate[lang]?.employees}
						</Button>
					}
				</div>
			</div>

			<Row>
				<Col lg={12}>
					<Card>
						<div
							className={`${hasData === 0 ? "text-center" : "table-style"} `}>
							{loading && (
								<div style={{ height: "300px" }}>
									<Loader />
								</div>
							)}
							{hasData === 1 && !loading && (
								<Table responsive>
									<thead>
										<tr className="text-center">
											<th>
												<strong>{Translate[lang]?.name}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.employee_id}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.civil_id}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.phone}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.email}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.department}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.job_title}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.salary}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.custody}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.action}</strong>
											</th>
										</tr>
									</thead>

									<tbody className="table-body">
										{data?.map((item, index) => {
											return (
												<CardItem
													index={index}
													key={index}
													item={item}
													setShouldUpdate={setShouldUpdate}
												/>
											);
										})}
									</tbody>
								</Table>
							)}
							{hasData === 0 && <NoData />}
							{/* <Pagination
								setData={setData}
								service={employeesService}
								shouldUpdate={shouldUpdate}
								setHasData={setHasData}
								setLoading={setLoading}
							/> */}
						</div>
					</Card>
				</Col>
			</Row>
		</Fragment>
	);
};

export default ActiveEmployees;
