import { useState } from "react";
import { useSelector } from "react-redux";
import JoinService from "../../../../services/JoinService";
import DeleteModal from "../../../common/DeleteModal";
import editIcon from "../../../../images/icons/edit.svg";
import trashIcon from "../../../../images/icons/delete.svg";
import RequestsService from "../../../../services/RequestsService";
import { Translate } from "../../../Enums/Tranlate";
import ConfirmModal from "../../../common/ConfirmModal";
import { toast } from "react-toastify";

const CardItem = ({
	item,
	setItem,
	index,
	setAddModal,
	setShouldUpdate,
	type,
}) => {
	const [deleteModal, setDeleteModal] = useState(false);
	const [editModal, setEditModal] = useState(false);
	const Auth = useSelector((state) => state.auth?.auth);
	const lang = useSelector((state) => state.auth?.lang);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const joinService = new JoinService();
	const requestsService = new RequestsService();

	const changeStatus = () => {
		let data = {
			status: "CLOSED",
		};
		requestsService.update(item?.id, data).then((res) => {
			if (res?.status) {
				toast.success(Translate[lang].updated_successfully);
				setShouldUpdate((prev) => !prev);
				setEditModal(false);
			}
		});
	};
	return (
		<tr key={index} className="text-center">
			{type === "job_application" && (
				<td>
					{item?.firstName} {item?.lastName}
				</td>
			)}
			{type === "job_application" && <td>{item?.jobTitle}</td>}
			{type === "job_application" && <td>{item?.position?.name || "-"}</td>}
			<td>{item?.phone}</td>
			{type === "job_application" && <td>{item?.email}</td>}
			{type === "job_application" && (
				<td>
					<a href={item?.cv} target="_blank" rel="noreferrer">
						<i
							className="la la-file-pdf"
							style={{ fontSize: "32px", color: "var(--primary)" }}
						/>
					</a>
				</td>
			)}
			{type !== "job_application" && <td>{item?.title}</td>}
			{type !== "job_application" && <td>{item?.subject}</td>}
			{type !== "job_application" && (
				<td>
					<span
						className={`badge ${
							item?.status === "OPEN" ? "success-outline" : "danger-outline"
						}`}>
						{Translate[lang][item?.status?.toLowerCase()]}
					</span>
				</td>
			)}
			<td>
				<div className="d-flex" style={{ gap: "20px" }}>
					{type !== "job_application" && item?.status === "OPEN" && (
						<img
							src={editIcon}
							className="cursor-pointer"
							onClick={() => {
								setEditModal(true);
							}}
							alt="edit"
							width={24}
						/>
					)}
					<img
						src={trashIcon}
						className="cursor-pointer"
						onClick={() => setDeleteModal(true)}
						alt="trash"
						width={24}
					/>
				</div>
			</td>
			{deleteModal && (
				<DeleteModal
					open={deleteModal}
					titleMsg={
						type === "job_application"
							? `${item?.firstName} ${item?.lastName}`
							: item?.title
					}
					deletedItem={item}
					modelService={
						type === "job_application" ? joinService : requestsService
					}
					onCloseModal={setDeleteModal}
					setShouldUpdate={setShouldUpdate}
				/>
			)}
			{editModal && (
				<ConfirmModal
					open={editModal}
					onCloseModal={() => setEditModal(false)}
					title={`${Translate[lang].edit} ${Translate[lang].status}`}
					body={`Edit Status For ${item?.title}`}
					submitButton={changeStatus}
					button={Translate[lang].confirm}
				/>
			)}
		</tr>
	);
};
export default CardItem;
