import React, { useEffect, useState } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import OrganizationService from "../../../../services/OrganizationService";
import { useLocation } from "react-router-dom";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { useSelector } from "react-redux";
import { Translate } from "../../../Enums/Tranlate";
import PhoneInput from "react-phone-input-2";
import "../style.scss";

const AddOrganization = () => {
	const location = useLocation();
	const [formData, setFormData] = useState({
		legalName: "",
		civilId: "",
		authorizedSignatory: "",
		establishmentDate: "",
		owner: "",
		address: "",
		description: "",
		fieldsOfWork: "",
		faxNumber: "",
		phoneNumber: "",
		yearDays: "",
		licenseNumber: "",
		commercialRegistration: "",
		legalOffice: "",
	});
	const [loading, setLoading] = useState(false);
	const [id, setId] = useState(null);
	const [view, setView] = useState(false);
	const navigate = useNavigate();
	const organizationService = new OrganizationService();
	const lang = useSelector((state) => state.auth?.lang);

	useEffect(() => {
		let item = location.state;
		if (
			location?.pathname.includes("edit") ||
			location?.pathname.includes("view")
		) {
			organizationService.find(item?.id).then((res) => {
				if (res?.status) {
					let data = res?.data?.data;
					setFormData({
						legalName: data?.legalName,
						civilId: data?.civilId,
						authorizedSignatory: data?.authorizedSignatory,
						establishmentDate: data?.establishmentDate?.split("T")[0],
						owner: data?.owner,
						address: data?.address,
						description: data?.description,
						fieldsOfWork: data?.fieldsOfWork,
						faxNumber: data?.faxNumber,
						phoneNumber: data?.phoneNumber,
						yearDays: data?.yearDays?.split("T")[0],
						licenseNumber: data?.licenseNumber,
						commercialRegistration: data?.commercialRegistration,
						legalOffice: data?.legalOffice,
					});
				}
				setLoading(false);
			});
		}
		if (location?.pathname.includes("view")) {
			setView(true);
		}
		if (location?.pathname.includes("edit")) {
			setId(item?.id);
		}
	}, [location]);

	const inputHandler = (e) => {
		setFormData({ ...formData, [e.target.name]: e.target.value });
	};

	const onSubmit = (e) => {
		e.preventDefault();

		let data = {
			...formData,
			establishmentDate: `${formData?.establishmentDate}T00:00:00.000Z`,
			yearDays: `${formData?.yearDays}T00:00:00.000Z`,
		};

		if (!!id) {
			setLoading(true);
			organizationService.update(id, data).then((response) => {
				if (response?.status === 200) {
					toast.success(response?.data?.message);
					navigate("/organization");
				}
				setLoading(false);
			});
		} else {
			setLoading(true);
			organizationService.create(data).then((response) => {
				if (response?.status === 201) {
					toast.success(response?.data?.message);
					navigate("/organization");
				}
				setLoading(false);
			});
		}
	};

	return (
		<>
			<div
				className="d-flex  align-items-center"
				style={{ gap: "8px", margin: "12px 0 32px" }}>
				<svg
					style={{
						cursor: "pointer",
						transform: lang === "ar" ? "rotate(180deg)" : "",
					}}
					width="24"
					height="24"
					viewBox="0 0 24 24"
					fill="none"
					xmlns="http://www.w3.org/2000/svg"
					onClick={() => navigate("/organization")}>
					<path
						d="M9.57 5.92993L3.5 11.9999L9.57 18.0699"
						stroke="#1E1F24"
						stroke-width="1.5"
						stroke-miterlimit="10"
						stroke-linecap="round"
						stroke-linejoin="round"
					/>
					<path
						d="M20.4999 12H3.66992"
						stroke="#1E1F24"
						stroke-width="1.5"
						stroke-miterlimit="10"
						stroke-linecap="round"
						stroke-linejoin="round"
					/>
				</svg>

				<label className="mb-0" style={{ fontSize: "20px", fontWeight: "600" }}>
					{view
						? Translate[lang]?.view
						: !!id
						? Translate[lang]?.edit
						: Translate[lang]?.add}{" "}
					{Translate[lang]?.organization}
				</label>
			</div>
			<Row className="add-organization">
				<Col md={10}>
					<AvForm onValidSubmit={onSubmit}>
						<div className="row">
							<div className="col-lg-6 col-sm-6 mb-3">
								<AvField
									label={`${Translate[lang]?.legal_name}`}
									name="legalName"
									type="text"
									value={formData?.legalName}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.legal_name}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 mb-3">
								<AvField
									label={`${Translate[lang]?.civil_id}`}
									name="civilId"
									type="text"
									value={formData?.civilId}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.civil_id}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 mb-3">
								<AvField
									label={`${Translate[lang]?.authorised_signatory}`}
									name="authorizedSignatory"
									type="text"
									value={formData?.authorizedSignatory}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.authorised_signatory}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 mb-3">
								<AvField
									label={`${Translate[lang]?.establishment_date}`}
									name="establishmentDate"
									type="date"
									value={formData?.establishmentDate}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.establishment_date}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 mb-3">
								<AvField
									label={`${Translate[lang]?.owner}`}
									name="owner"
									type="text"
									value={formData?.owner}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.owner}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 mb-3">
								<AvField
									label={`${Translate[lang]?.address}`}
									name="address"
									type="text"
									value={formData?.address}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.address}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 mb-3">
								<AvField
									label={`${Translate[lang]?.description}`}
									name="description"
									type="text"
									value={formData?.description}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.description}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 mb-3">
								<AvField
									label={`${Translate[lang]?.fields_of_work}`}
									name="fieldsOfWork"
									type="text"
									value={formData?.fieldsOfWork}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.fields_of_work}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 mb-3">
								<label>{Translate[lang]?.phone}</label>
								<PhoneInput
									country={"kw"}
									enableSearch={true}
									className="w-100"
									value={formData?.phoneNumber}
									onChange={(e) => setFormData({ ...formData, phoneNumber: e })}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 mb-3">
								<label>{Translate[lang]?.fax_number}</label>
								<PhoneInput
									country={"kw"}
									enableSearch={true}
									className="w-100"
									value={formData?.faxNumber}
									onChange={(e) => setFormData({ ...formData, faxNumber: e })}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 mb-3">
								<AvField
									label={`${Translate[lang]?.year_days}`}
									name="yearDays"
									type="date"
									value={formData?.yearDays}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.year_days}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 mb-3">
								<AvField
									label={`${Translate[lang]?.license_number}`}
									name="licenseNumber"
									type="text"
									value={formData?.licenseNumber}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.license_number}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 mb-3">
								<AvField
									label={`${Translate[lang]?.commercial_registration}`}
									name="commercialRegistration"
									type="text"
									value={formData?.commercialRegistration}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.commercial_registration}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 mb-3">
								<AvField
									label={`${Translate[lang]?.legal_office}`}
									name="legalOffice"
									type="text"
									value={formData?.legalOffice}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.legal_office}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 mt-2 mb-5">
								{view && (
									<Button
										variant="secondary"
										type="button"
										className="w-100 fs-14"
										onClick={() => navigate("/organization")}>
										{Translate[lang]?.cancel}
									</Button>
								)}
								{!view && (
									<Button
										variant="primary"
										type="submit"
										className="w-100 fs-14"
										disabled={loading}>
										{Translate[lang]?.save}
									</Button>
								)}
							</div>
						</div>
					</AvForm>
				</Col>
			</Row>
		</>
	);
};

export default AddOrganization;
