import React from "react";
import { useState } from "react";
import {
  Row,
  Col,
  Card,
  Table,
  Button,
} from "react-bootstrap";
import { useSelector } from "react-redux";
import Loader from "../../common/Loader";
import NoData from "../../common/NoData";
import Pagination from "../../common/Pagination/Pagination";
import { Translate } from "../../Enums/Tranlate";
import CardItem from "./CardItem";
import AddFacilitiesModal from "./AddFacilitiesModal";
import DepartmentService from "../../../services/DepartmentService";
import './style.scss'

const Facilities = () => {
    const [data, setData] = useState([])
    const [addModal, setAddModal] = useState(false)
    const [item, setItem] = useState({})
    const [hasData, setHasData] = useState(null)
    const [loading, setLoading] = useState(false)
    const [shouldUpdate, setShouldUpdate] = useState(false)
    const lang = useSelector(state=> state.auth?.lang)
    const departmentService = new DepartmentService()
    const Auth = useSelector((state) => state.auth?.auth);
    const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);

  return (
    <div className="department">
      <div className="mb-4">
        <div className="search-card d-flex justify-content-between align-items-center">
          <div>
            <span style={{
              fontWeight: '600',
              textTransform: 'capitalize',
              color: '#000',
            }}>{Translate[lang].facilities}</span>
          </div>
          <div className="add-department">
            {<Button variant="primary" className='me-2 h-75' onClick={()=> { 
              setItem({})
              setAddModal(true) 
            }}>
              <i className="la la-plus mx-1"></i>
              {Translate[lang]?.add} {Translate[lang]?.facility}
            </Button>}
          </div>
        </div >
      </div>
      
      <Row>
        <Col lg={12}>
          <Card>
            <div className={`${hasData === 0 ? 'text-center' : 'table-style'} `}>
              {loading && <div style={{height: '400px'}}>
                <Loader />
              </div>}
              
              {(hasData === 1 && !loading) && <Table responsive>
                <thead>
                  <tr className='text-center'>
                    <th>
                      <strong>{Translate[lang]?.facility_type}</strong>
                    </th>
                    <th>
                      <strong>{Translate[lang]?.facility_plan}</strong>
                    </th>
                    <th>
                      <strong>{Translate[lang]?.scheduled_time}</strong>
                    </th>
                    <th>
                      <strong>{Translate[lang]?.assigned_branches}</strong>
                    </th>
                    <th>
                      <strong>{Translate[lang]?.action}</strong>
                    </th>
                  </tr>
                </thead>

                <tbody className="table-body">
                    {data?.map((item, index) =>{
                        return <CardItem 
                            index= {index}
                            key= {index}
                            item={item}
                            setItem={setItem}
                            setAddModal={setAddModal}
                            setShouldUpdate={setShouldUpdate}
                        />
                    })}
                </tbody>
              </Table>}
              {hasData === 0 && <NoData />}
              <Pagination
                  setData={setData}
                  service={departmentService}
                  shouldUpdate={shouldUpdate}
                  setHasData={setHasData}
                  setLoading={setLoading}
              />
            </div>
          </Card>
        </Col>
      </Row>

      {addModal && 
        <AddFacilitiesModal
          item={item} 
          addModal={addModal} 
          setShouldUpdate={setShouldUpdate}
          setAddModal={()=> setAddModal(false)}
          Service={{}}
      />}
    </div>
  );
};

export default Facilities;
