import { useEffect, useState } from "react";
import { Button, Col, Modal, Row } from "react-bootstrap";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import EmployeesService from "../../../../services/EmployeesService";
import { Translate } from "../../../Enums/Tranlate";
import Select from "react-select";

const ResignationModal = ({
	resignationModal,
	setResignationModal,
	item,
	setShouldUpdate,
}) => {
	const [formData, setFormData] = useState({
		start_resignation_date: "",
		end_resignation_date: "",
		employee: "",
	});
	const [loading, setLoading] = useState(false);
	const [employeesOptions, setEmployeesOptions] = useState([]);
	const employeesService = new EmployeesService();
	const lang = useSelector((state) => state.auth.lang);

	useEffect(() => {
		let params = {
			is_delete: false,
			is_resignation: false,
		};
		employeesService?.getList(params).then((res) => {
			if (res?.status === 200) {
				let data = res?.data?.data?.data?.map((emp) => {
					return {
						...emp,
						label: emp?.name,
						value: emp.id,
					};
				});
				setEmployeesOptions(data);
			}
		});
	}, []);

	useEffect(() => {
		setFormData({
			...formData,
		});
	}, [item]);

	const submit = (e) => {
		e.preventDefault();
		let data = {
			start_resignation_date: formData.start_resignation_date,
			end_resignation_date: formData.end_resignation_date,
		};

		setLoading(true);
		employeesService
			.resignation(formData?.employee?.id, data)
			?.then((res) => {
				if (res && res?.status === 201) {
					toast.success("Resignation Employee Successfully");
					setShouldUpdate((prev) => !prev);
					setResignationModal();
				}
				setLoading(false);
			})
			.catch(() => setLoading(false));
	};

	return (
		<Modal
			className={lang === "en" ? "en fade addProduct" : "ar fade addProduct"}
			style={{ textAlign: lang === "en" ? "left" : "right" }}
			show={resignationModal}
			onHide={() => {
				setResignationModal();
			}}>
			<AvForm className="form-horizontal" onValidSubmit={submit}>
				<Modal.Header>
					<Modal.Title>
						{Translate[lang]?.resignation}{" "}
						<span className="text-danger">{formData?.employee?.name}</span>
					</Modal.Title>
					<Button
						variant=""
						className="close"
						style={{
							right: lang === "en" ? "0" : "auto",
							left: lang === "ar" ? "0" : "auto",
						}}
						onClick={() => {
							setResignationModal();
						}}>
						<span>&times;</span>
					</Button>
				</Modal.Header>
				<Modal.Body>
					<Row>
						<Col md={12} className="mb-3">
							<label className="text-label">{Translate[lang].employee}</label>
							<Select
								placeholder={Translate[lang]?.select}
								options={employeesOptions}
								value={formData.employee}
								onChange={(e) => setFormData({ ...formData, employee: e })}
							/>
						</Col>
						<Col md={12}>
							<AvField
								label={Translate[lang]?.start_resignation_date}
								type="date"
								placeholder={Translate[lang]?.start_resignation_date}
								bsSize="lg"
								name="start_resignation_date"
								max={formData.end_resignation_date}
								validate={{
									required: {
										value: true,
										errorMessage: Translate[lang].field_required,
									},
								}}
								value={formData.start_resignation_date}
								onChange={(e) =>
									setFormData({
										...formData,
										start_resignation_date: e.target.value,
									})
								}
							/>
						</Col>
						<Col md={12}>
							<AvField
								label={Translate[lang]?.end_resignation_date}
								type="date"
								min={formData.start_resignation_date}
								placeholder={Translate[lang]?.end_resignation_date}
								bsSize="lg"
								name="end_resignation_date"
								validate={{
									required: {
										value: true,
										errorMessage: Translate[lang].field_required,
									},
								}}
								value={formData.end_resignation_date}
								onChange={(e) =>
									setFormData({
										...formData,
										end_resignation_date: e.target.value,
									})
								}
							/>
						</Col>
					</Row>
				</Modal.Body>
				<Modal.Footer>
					<Button
						variant="primary"
						type="submit"
						className="w-100"
						disabled={loading}>
						{Translate[lang]?.save}
					</Button>
				</Modal.Footer>
			</AvForm>
		</Modal>
	);
};

export default ResignationModal;
