import { useEffect, useState } from "react";
import { Button, Col, Form, Modal, Row } from "react-bootstrap";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { Translate } from "../../../Enums/Tranlate";
import Select from "react-select";
import "../style.scss";
import PositionsService from "../../../../services/PositionsService";

const AddPositionsModal = ({
	addModal,
	setAddModal,
	item,
	setShouldUpdate,
}) => {
	const [position, setPosition] = useState("");
	const [formData, setFormData] = useState([""]);
	const [isAdd, setIsAdd] = useState(false);
	const [loading, setLoading] = useState(false);
	const lang = useSelector((state) => state.auth.lang);
	const positionsService = new PositionsService();

	useEffect(() => {
		if (Object.keys(item)?.length === 0) {
			setIsAdd(true);
		} else {
			setIsAdd(false);
			setPosition(item?.name);
		}
	}, [item]);

	const submit = (e) => {
		e.preventDefault();

		let data = {};
		setLoading(true);
		if (isAdd) {
			if (!!formData?.filter((res) => !!res)?.length) {
				data["name"] = [position, ...formData?.filter((res) => !!res)];
			} else {
				data["name"] = position;
			}
			positionsService
				.create(data)
				?.then((res) => {
					if (res && res?.status === 201) {
						toast.success(`${Translate[lang]?.added_successfully}`);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		} else {
			data["name"] = position;
			positionsService
				.update(item?.id, data)
				?.then((res) => {
					if (res && res?.status === 200) {
						toast.success(`${Translate[lang]?.updated_successfully}`);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	return (
		<Modal
			className={lang === "en" ? "en fade addPosition" : "ar fade addPosition"}
			style={{ textAlign: lang === "en" ? "left" : "right" }}
			show={addModal}
			onHide={() => {
				setAddModal();
			}}>
			<AvForm className="form-horizontal" onValidSubmit={submit}>
				<Modal.Body>
					<Row>
						<Col md={12} className="mb-2">
							<div className="d-flex justify-content-between">
								<p className="mb-0">
									{Translate[lang].add_new_position} (
									{Translate[lang].for_hiring})
								</p>
								<Button
									variant=""
									className="close"
									style={{
										right: lang === "en" ? "0" : "auto",
										left: lang === "ar" ? "0" : "auto",
									}}
									onClick={() => {
										setAddModal();
									}}>
									<span style={{ fontSize: "22px" }}>&times;</span>
								</Button>
							</div>
						</Col>
						<Col md={12}>
							<AvField
								label={`${Translate[lang]?.position} (${Translate[lang].for_hiring})`}
								type="text"
								placeholder={Translate[lang]?.position}
								bsSize="lg"
								name="position"
								validate={{
									required: {
										value: true,
										errorMessage: Translate[lang].field_required,
									},
								}}
								value={position}
								onChange={(e) => setPosition(e.target.value)}
							/>
						</Col>
						{isAdd && (
							<Col md={12}>
								<label className="d-block">
									{Translate[lang]?.position} 2 ({Translate[lang]?.optional})
								</label>
								{formData?.map((posi, index) => {
									return (
										<div className="d-flex justify-content-between" key={index}>
											<div className="w-75">
												<AvField
													type="text"
													placeholder={Translate[lang]?.position}
													bsSize="lg"
													name={`position${index}`}
													value={posi}
													onChange={(e) => {
														let update = formData?.map((res, ind) => {
															if (ind === index) {
																return e.target.value;
															} else {
																return res;
															}
														});
														setFormData(update);
													}}
												/>
											</div>
											{index + 1 === formData?.length && (
												<i
													className="la la-plus cursor-pointer"
													onClick={() => setFormData([...formData, ""])}
												/>
											)}
										</div>
									);
								})}
							</Col>
						)}
					</Row>
				</Modal.Body>
				<Modal.Footer>
					<Button
						variant="primary"
						type="submit"
						className="w-100"
						disabled={loading}>
						{Translate[lang]?.confirm}
					</Button>
				</Modal.Footer>
			</AvForm>
		</Modal>
	);
};

export default AddPositionsModal;
