import { useState } from "react";
import { Badge, Dropdown } from "react-bootstrap";
import { useSelector } from "react-redux";
import DeleteModal from "../../../common/DeleteModal";
import { Translate } from "../../../Enums/Tranlate";
import BlogsService from "../../../../services/BlogsService";
import { Link, useNavigate } from "react-router-dom";
import editIcon from "../../../../images/icons/edit.svg";
import trashIcon from "../../../../images/icons/delete.svg";
import viewIcon from "../../../../images/icons/eye.svg";

const CardItem = ({
	item,
	setItem,
	index,
	setAddModal,
	setView,
	setShouldUpdate,
}) => {
	const [deleteModal, setDeleteModal] = useState(false);
	const Auth = useSelector((state) => state.auth?.auth);
	const lang = useSelector((state) => state.auth?.lang);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const blogsService = new BlogsService();
	const navigate = useNavigate();

	return (
		<tr key={index} className="table-row">
			<td
				dangerouslySetInnerHTML={{
					__html: item?.title?.replaceAll("&lt;", "<"),
				}}></td>
			{/* <td>{item?.description}</td> */}
			<td>{item?.readTime}</td>
			<td>{item?.category}</td>
			<td>{item?.publishedDate?.split("T")[0]}</td>
			<td>
				<span
					className={`badge ${
						item?.status === "PUBLISHED" ? "success-outline" : "warning-outline"
					}`}>
					{Translate[lang][item?.status?.toLowerCase()]}
				</span>
			</td>
			<td>
				<div className="d-flex justify-content-center" style={{ gap: "20px" }}>
					<Link
						to={`/blogs/view`}
						state={item}
						style={{
							color: "#0F50A6",
							fontWeight: "500",
						}}>
						<img src={viewIcon} alt="view" width={24} />
					</Link>
					<Link
						to={`/blogs/edit`}
						state={item}
						style={{
							color: "#0F50A6",
							fontWeight: "500",
						}}>
						<img
							src={editIcon}
							className="cursor-pointer"
							alt="edit"
							width={24}
						/>
					</Link>
					<img
						src={trashIcon}
						className="cursor-pointer"
						onClick={() => setDeleteModal(true)}
						alt="trash"
						width={24}
					/>
				</div>
			</td>
			{deleteModal && (
				<DeleteModal
					open={deleteModal}
					titleMsg={item?.title?.replaceAll("&lt;", "<")}
					deletedItem={item}
					modelService={blogsService}
					onCloseModal={setDeleteModal}
					setShouldUpdate={setShouldUpdate}
				/>
			)}
		</tr>
	);
};
export default CardItem;
