import React, { Fragment, useEffect } from "react";
import { useState } from "react";
import { Row, Col, Card, Table, Button } from "react-bootstrap";
import { useSelector } from "react-redux";
import Loader from "../../common/Loader";
import NoData from "../../common/NoData";
import Pagination from "../../common/Pagination/Pagination";
import { Translate } from "../../Enums/Tranlate";
import CardItem from "./CardItem";
import AddSubjectsModal from "./AddSubjectsModal";
import DepartmentService from "../../../services/DepartmentService";
import "./style.scss";
import { useLocation, useNavigate } from "react-router-dom";

const Education = () => {
	const [data, setData] = useState([
		{
			id: 1,
			subject: "Sub Name",
			type: "Education",
			grade: 3,
			program_name: "Program Name",
			subjects: ["English", "Arabic"],
		},
	]);
	const [addModal, setAddModal] = useState(false);
	const [item, setItem] = useState({});
	const [hasData, setHasData] = useState(1);
	const [type, setType] = useState("subjects");
	const [loading, setLoading] = useState(false);
	const [shouldUpdate, setShouldUpdate] = useState(false);
	const lang = useSelector((state) => state.auth?.lang);
	const departmentService = new DepartmentService();
	const Auth = useSelector((state) => state.auth?.auth);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const navigate = useNavigate();
	const location = useLocation();

	useEffect(() => {
		if (!!location?.state) setType(location?.state);
	}, []);

	return (
		<div className="department">
			<div className="tabs-div">
				<p
					onClick={() => setType("subjects")}
					className={type === "subjects" ? "activeP" : ""}>
					{Translate[lang].subjects}
				</p>
				<p
					onClick={() => setType("programs")}
					className={type === "programs" ? "activeP" : ""}>
					{Translate[lang].programs}
				</p>
			</div>
			<div className="mb-4">
				<div className="search-card d-flex justify-content-between align-items-center">
					<div>
						<span
							style={{
								fontWeight: "600",
								textTransform: "capitalize",
								color: "#000",
							}}>
							{Translate[lang][type]}
						</span>
					</div>
					<div className="add-department">
						{
							<Button
								variant="primary"
								className="me-2 h-75"
								onClick={() => {
									if (type === "subjects") {
										setItem({});
										setAddModal(true);
									} else {
										navigate("/education/add-program");
									}
								}}>
								<i className="la la-plus mx-1"></i>
								{Translate[lang]?.add}{" "}
								{type === "subjects"
									? Translate[lang]?.subject
									: Translate[lang]?.program}
							</Button>
						}
					</div>
				</div>
			</div>

			<Row>
				<Col lg={12}>
					<Card>
						<div
							className={`${hasData === 0 ? "text-center" : "table-style"} `}>
							{loading && (
								<div style={{ height: "400px" }}>
									<Loader />
								</div>
							)}

							{hasData === 1 && !loading && (
								<Table responsive>
									<thead>
										<tr className="text-center">
											<th>
												<strong>
													{type === "subjects"
														? Translate[lang]?.subjects
														: Translate[lang]?.program_name}
												</strong>
											</th>
											<th>
												<strong>
													{type === "subjects"
														? Translate[lang]?.education_type
														: Translate[lang]?.type}
												</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.grade}</strong>
											</th>
											{type === "subjects" && (
												<th>
													<strong>{Translate[lang]?.subjects}</strong>
												</th>
											)}
											<th>
												<strong>{Translate[lang]?.action}</strong>
											</th>
										</tr>
									</thead>

									<tbody className="table-body">
										{data?.map((item, index) => {
											return (
												<CardItem
													index={index}
													key={index}
													item={item}
													setItem={setItem}
													type={type}
													setAddModal={setAddModal}
													setShouldUpdate={setShouldUpdate}
												/>
											);
										})}
									</tbody>
								</Table>
							)}
							{hasData === 0 && <NoData />}
							{/* <Pagination
                  setData={setData}
                  service={departmentService}
                  shouldUpdate={shouldUpdate}
                  setHasData={setHasData}
                  setLoading={setLoading}
              /> */}
						</div>
					</Card>
				</Col>
			</Row>

			{addModal && (
				<AddSubjectsModal
					item={item}
					addModal={addModal}
					setShouldUpdate={setShouldUpdate}
					setAddModal={() => setAddModal(false)}
					Service={{}}
				/>
			)}
		</div>
	);
};

export default Education;
