import { useState } from "react";
import { useSelector } from "react-redux";
import EmployeesService from "../../../../../services/EmployeesService";
import { Translate } from "../../../../Enums/Tranlate";
import DeleteModal from "../../DeleteModal";
import { Link } from "react-router-dom";
import editIcon from "../../../../../images/icons/edit.svg";
import trashIcon from "../../../../../images/icons/delete.svg";
import viewIcon from "../../../../../images/icons/eye.svg";

const CardItem = ({ item, index, setShouldUpdate }) => {
	const [deleteModal, setDeleteModal] = useState(false);
	const Auth = useSelector((state) => state.auth?.auth);
	const lang = useSelector((state) => state.auth?.lang);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const employeesService = new EmployeesService();

	return (
		<tr key={index} className="text-center">
			<td>
				<Link
					to={`/employees/profile/${item.id}`}
					state={item}
					className="profile-link">
					{item?.name}
				</Link>
			</td>
			<td>{"-"}</td>
			<td>{item?.civil_id}</td>
			<td>{item?.phone || "-"}</td>
			<td>{item.personal_email}</td>
			<td>{item.department?.name}</td>
			<td>{item.job_title}</td>
			<td>
				{item.salary} {Translate[lang].kwd}
			</td>
			<td>{item.employee_assets?.length}</td>
			<td>
				<div className="d-flex" style={{ gap: "20px" }}>
					<Link to={`/employees/profile/${item.id}`} state={item}>
						<img src={viewIcon} onClick={() => {}} alt="edit" width={24} />
					</Link>
					<img
						className="cursor-pointer"
						src={editIcon}
						onClick={() => {}}
						alt="edit"
						width={24}
					/>
					<img
						className="cursor-pointer"
						src={trashIcon}
						alt="trash"
						width={24}
					/>
				</div>
			</td>
			{deleteModal && (
				<DeleteModal
					open={deleteModal}
					titleMsg={item?.name}
					deletedItem={item}
					modelService={employeesService}
					onCloseModal={setDeleteModal}
					setShouldUpdate={setShouldUpdate}
					type="delete"
				/>
			)}
		</tr>
	);
};
export default CardItem;
