import { useEffect, useState } from "react";
import { Button, Card, Table } from "react-bootstrap";
import { Translate } from "../../../Enums/Tranlate";
import { useSelector } from "react-redux";
import { AvForm } from "availity-reactstrap-validation";
import Select from "react-select";
import MonthDropDown from "../../../Enums/MonthDropDown";
import YearDropDown from "../../../Enums/YearDropDown";
import Loader from "../../../common/Loader";
import NoData from "../../../common/NoData";
import AllowanceService from "../../../../services/AllowanceService";
import { toast } from "react-toastify";
import AddAssetsModal from "../../Assets/AddAssetsModal";
import AddAllowancesModal from "../../Allowances/AddAllowancesModal";

const Allowances = ({ employee }) => {
	const initial = {
		name: "",
		type: "",
	};
	const [formData, setFormData] = useState(initial);
	const [addAssetsModal, setAddAssetsModal] = useState(false);
	const [addAllowancesModal, setAddAllowancesModal] = useState(false);
	const [view, setView] = useState(false);
	const [shouldUpdate, setShouldUpdate] = useState(false);
	const [showForm, setShowForm] = useState(false);
	const [loading, setLoading] = useState(false);
	const [data, setData] = useState([]);
	const [total, setTotal] = useState(0);
	const [hasAllowances, setHasAllowances] = useState(null);
	const [hasCustodies, setHasCustodies] = useState(0);
	const [hasLoan, setHasLoan] = useState(0);
	const [allowanceOptions, setAllowanceOptions] = useState(null);
	const typesOptions = [
		{ label: "Add to Payroll", value: "add_to_payroll", type: "payroll" },
		{ label: "Select Range", value: "select_range", type: "range" },
		{
			label: "Only Calculation",
			value: "only_calculation",
			type: "calculation",
		},
	];
	const [type, setType] = useState(typesOptions[0]);
	const lang = useSelector((state) => state.auth.lang);
	const initialParams = {
		month_from: "",
		year_from: "",
		month_to: "",
		year_to: "",
	};
	const [params, setParams] = useState(initialParams);
	const allowanceService = new AllowanceService();

	useEffect(() => {
		allowanceService.getList().then((res) => {
			if (res?.status) {
				let data = res?.data?.data?.data?.map((info) => {
					return {
						label: info?.name,
						value: info?.name,
						fee: info?.fee,
						id: info?.id,
					};
				});
				setAllowanceOptions(data);
			}
		});
	}, []);

	useEffect(() => {
		let typeVal = type?.type;
		if (!!typeVal) {
			setLoading(true);
			allowanceService
				.getAllowanceForEmp(employee?.id, typeVal)
				.then((res) => {
					if (res?.status) {
						let data = res?.data?.data?.data;
						setData(data);
						data?.length === 0 ? setHasAllowances(0) : setHasAllowances(1);
						let total = data
							?.map((tot) => tot.fee)
							.reduce(
								(accumulator, currentValue) => accumulator + currentValue,
								0
							);
						setTotal(total);
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	}, [type]);

	const changeParams = (e, name, type) => {
		setParams({ ...params, [`${name}_${type}`]: e });
	};

	const onSubmit = (e) => {
		e.preventDefault();

		let data = {
			employee_id: employee?.id,
			allowance_id: formData?.name?.id,
			type: formData?.type?.type,
			month_from: params?.month_from?.value,
			year_from: params?.year_from?.value,
		};

		if (formData?.type?.type !== "payroll") {
			data["month_to"] = params?.month_to?.value;
			data["year_to"] = params?.year_to?.value;
		}
		allowanceService
			.allowanceForEmp(data)
			.then((res) => {
				if (res?.status) {
					toast.success(
						`${Translate[lang].add} ${Translate[lang].successfully}`
					);
					setFormData(initial);
					setParams(initialParams);
					setType(formData?.type);
				}
			})
			.catch((e) => console.error(e));
	};

	if (loading) {
		return <Loader />;
	}

	return (
		<>
			<div className="mb-4">
				<div className="mb-3 d-flex align-items-center justify-content-between">
					<span
						style={{
							fontWeight: "600",
							textTransform: "capitalize",
							color: "#000",
						}}>
						{Translate[lang].allowances}
					</span>
					<Button variant="primary" onClick={() => setAddAllowancesModal(true)}>
						{Translate[lang].add} {Translate[lang].allowances}
					</Button>
				</div>
				<div
					className={`${hasAllowances === 0 ? "text-center" : "table-style"} `}>
					{hasAllowances === 1 && !loading && (
						<Table responsive>
							<thead>
								<tr className="text-center">
									<th>
										<strong>{Translate[lang]?.type}</strong>
									</th>
									<th>
										<strong>{Translate[lang]?.fees}</strong>
									</th>
									<th>
										<strong>{Translate[lang]?.payment}</strong>
									</th>
									<th>
										<strong>{Translate[lang]?.assign_date}</strong>
									</th>
								</tr>
							</thead>

							<tbody className="table-body">
								{data?.map((item, index) => {
									return (
										<tr key={index} className="text-center">
											<td>{item?.allowance_type}</td>
											<td>{item?.allowance_fee}</td>
											<td>{item?.payment}</td>
											<td>{item?.assign_date}</td>
										</tr>
									);
								})}
							</tbody>
						</Table>
					)}
					{hasAllowances === 0 && !loading && (
						<div className="text-center">
							<NoData />
						</div>
					)}
				</div>
			</div>
			<div className="mb-4">
				<div className="mb-3 d-flex align-items-center justify-content-between">
					<span
						style={{
							fontWeight: "600",
							textTransform: "capitalize",
							color: "#000",
						}}>
						{Translate[lang].custody}
					</span>
					<Button
						variant="primary"
						onClick={() => {
							setAddAssetsModal(true);
							setView(false);
						}}>
						{Translate[lang].add} {Translate[lang].custody}
					</Button>
				</div>
				<div
					className={`${hasCustodies === 0 ? "text-center" : "table-style"} `}>
					{hasCustodies === 1 && !loading && (
						<Table responsive>
							<thead>
								<tr className="text-center">
									<th>
										<strong>{Translate[lang]?.custody_name}</strong>
									</th>
									<th>
										<strong>{Translate[lang]?.custody_type}</strong>
									</th>
									<th>
										<strong>{Translate[lang]?.type}</strong>
									</th>
									<th>
										<strong>{Translate[lang]?.action}</strong>
									</th>
								</tr>
							</thead>

							<tbody className="table-body">
								{data?.map((item, index) => {
									return (
										<tr key={index} className="text-center">
											<td>{item?.name}</td>
											<td>{item?.custody_type}</td>
											<td>{item?.type}</td>
											<td>
												<Button variant="primary-outline">
													{Translate[lang].hand_over}
												</Button>
											</td>
										</tr>
									);
								})}
							</tbody>
						</Table>
					)}
					{hasCustodies === 0 && !loading && (
						<div className="text-center">
							<NoData />
						</div>
					)}
				</div>
			</div>
			<div className="mb-4">
				<div className="mb-3 d-flex align-items-center justify-content-between">
					<span
						style={{
							fontWeight: "600",
							textTransform: "capitalize",
							color: "#000",
						}}>
						{Translate[lang].loan}
					</span>
					<Button variant="primary" onClick={() => setShowForm(true)}>
						{Translate[lang].add} {Translate[lang].loan}
					</Button>
				</div>
				<div className={`${hasLoan === 0 ? "text-center" : "table-style"} `}>
					{hasLoan === 1 && !loading && (
						<Table responsive>
							<thead>
								<tr className="text-center">
									<th>
										<strong>{Translate[lang]?.employee_name}</strong>
									</th>
									<th>
										<strong>{Translate[lang]?.department}</strong>
									</th>
									<th>
										<strong>{Translate[lang]?.loan}</strong>
									</th>
									<th>
										<strong>{Translate[lang]?.no_of_installments}</strong>
									</th>
									<th>
										<strong>{Translate[lang]?.no_of_payments_made}</strong>
									</th>
									<th>
										<strong>{Translate[lang]?.remaining}</strong>
									</th>
									<th>
										<strong>{Translate[lang]?.installments}</strong>
									</th>
									<th>
										<strong>{Translate[lang]?.payment_start_date}</strong>
									</th>
									<th>
										<strong>{Translate[lang]?.note}</strong>
									</th>
								</tr>
							</thead>

							<tbody className="table-body">
								{data?.map((item, index) => {
									return (
										<tr key={index} className="text-center">
											<td>{item?.employee?.name}</td>
											<td>{item?.employee?.department?.name}</td>
											<td>{item?.loan}</td>
											<td>{item?.no_of_installments}</td>
											<td>{item?.no_of_payments_made}</td>
											<td>
												{item?.no_of_installments - item?.no_of_payments_made}
											</td>
											<td
												style={{
													display: "grid",
													gap: "8px",
													gridTemplateColumns: "auto auto auto",
												}}>
												{item?.loan_dates?.map((res) => {
													return (
														<p
															onClick={() => {
																// if (
																// 	isExist("edit_loans") &&
																// 	selectTab === "has_loans"
																// ) {
																// 	setFreezMonth(res);
																// 	setFreez(true);
																// }
															}}
															key={res?.id}
															style={{
																padding: "8px",
																border: "1px solid #dedede",
																borderRadius: "5px",
																textAlign: "center",
																cursor: "pointer",
																gap: "8px",
																textDecoration: res?.is_finished
																	? "line-through"
																	: "",
															}}
															className="d-flex mb-0">
															<span>
																{Translate[lang][res?.month?.toLowerCase()]}
															</span>
															<span>{res?.year}</span>
														</p>
													);
												})}
											</td>
											<td>{item.start_date?.split("T")[0]}</td>
											<td>{item.note || "-"}</td>
										</tr>
									);
								})}
							</tbody>
						</Table>
					)}
					{hasLoan === 0 && !loading && (
						<div className="text-center">
							<NoData />
						</div>
					)}
				</div>
			</div>
			<div className="row mb-5">
				<div className="col-5">
					<Button className="w-100" variant="primary">
						{Translate[lang].print}
					</Button>
				</div>
			</div>

			{addAssetsModal && (
				<AddAssetsModal
					item={{}}
					addModal={addAssetsModal}
					setShouldUpdate={setShouldUpdate}
					setAddModal={() => {
						setAddAssetsModal(false);
						setView(false);
					}}
					view={view}
				/>
			)}
			{addAllowancesModal && (
				<AddAllowancesModal
					item={{}}
					addModal={addAllowancesModal}
					setShouldUpdate={setShouldUpdate}
					setAddModal={() => {
						setAddAllowancesModal(false);
					}}
				/>
			)}
		</>
	);
};
export default Allowances;
