import { useEffect, useState } from "react";
import { Button, Card, Col, Row, Form } from "react-bootstrap";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { useLocation, useNavigate } from "react-router-dom";
import { Translate } from "../../../Enums/Tranlate";
import Loader from "../../../common/Loader";
import RoleService from "../../../../services/RoleService";
import "../style.scss";

const AddPermission = () => {
	const [formData, setFormData] = useState({
		name: "",
		isActive: true,
	});
	const rolesFun = ["add", "edit", "view", "delete"];
	const [loading, setLoading] = useState(false);
	const [rolesData, setRolesData] = useState([]);
	const navigate = useNavigate();
	const location = useLocation();
	const roleService = new RoleService();
	const Auth = useSelector((state) => state.auth?.auth);
	const lang = useSelector((state) => state?.auth.lang);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	let id = location?.state?.id;

	useEffect(() => {
		setLoading(true);
		let requests = [roleService.getModule()];
		if (!!location?.state?.id)
			requests = [
				roleService.getModule(),
				roleService.find(location?.state?.id),
			];
		Promise.all(requests).then((res) => {
			let response1 = [];
			if (res[0]?.status) {
				response1 = res[0]?.data?.data
					?.filter((res) => res?.isActive)
					?.map((rol) => {
						return {
							...rol,
							add: false,
							edit: false,
							delete: false,
							view: false,
							name: rol?.name?.toLowerCase(),
							displayName: rol?.displayName
								?.toLowerCase()
								.replaceAll(" ", "_")
								.replaceAll("_&", ""),
							children: !rol?.children
								? []
								: rol?.children
										?.filter((res) => res?.isActive)
										?.map((child) => {
											return {
												...child,
												add: false,
												edit: false,
												delete: false,
												view: false,
												name: child?.name?.toLowerCase(),
												displayName: child?.displayName
													?.toLowerCase()
													.replaceAll(" ", "_")
													.replaceAll("_&", ""),
											};
										}),
						};
					});
			}
			if (!!location?.state?.id && res[1]?.status) {
				setFormData({
					name: res[1]?.data?.data?.name,
					isActive: res[1]?.data?.data?.isActive,
				});
				let resFind = res[1]?.data?.data?.permissions;
				response1 = response1?.map((response) => {
					let test = resFind.find((info) => info?.moduleId === response?.id);
					return {
						...response,
						add: test?.canAdd,
						edit: test?.canEdit,
						delete: test?.canDelete,
						view: test?.canView,
						children: response?.children?.map((child) => {
							let childRes = resFind.find(
								(info) => info?.moduleId === child?.id
							);
							return {
								...child,
								add: childRes?.canAdd,
								edit: childRes?.canEdit,
								delete: childRes?.canDelete,
								view: childRes?.canView,
							};
						}),
					};
				});
			}
			setRolesData(
				response1?.sort((a, b) => {
					const contentA = a.children || [];
					const contentB = b.children || [];

					if (contentA.length === 0 && contentB.length !== 0) return -1;
					if (contentA.length !== 0 && contentB.length === 0) return 1;
					return contentA.length - contentB.length;
				})
			);
			setLoading(false);
		});
	}, []);

	const onSubmit = (e) => {
		e.preventDefault();

		let permissions = rolesData?.flatMap((res) => {
			const parentPermission = {
				moduleId: res?.id,
				canView: res?.view,
				canAdd: res?.add,
				canEdit: res?.edit,
				canDelete: res?.delete,
			};
			const childPermissions =
				res?.children?.map((child) => ({
					moduleId: child?.id,
					canView: child?.view,
					canAdd: child?.add,
					canEdit: child?.edit,
					canDelete: child?.delete,
				})) || [];

			return [parentPermission, ...childPermissions];
		});
		let data = {
			name: formData?.name,
			permissions: permissions,
		};

		if (!!location?.state?.id) {
			data["isActive"] = formData?.isActive;
			roleService.update(location?.state?.id, data).then((res) => {
				if (res?.status === 200) {
					toast.success(res?.data?.message);
					navigate("/roles");
				}
			});
		} else {
			data["platform"] = "ADMIN";
			roleService.create(data).then((res) => {
				if (res?.status === 201) {
					toast.success(res?.data?.message);
					navigate("/roles");
				}
			});
		}
	};

	if (loading) {
		return (
			<Card style={{ height: "300px" }}>
				<Card.Body>
					<Loader />
				</Card.Body>
			</Card>
		);
	}
	return (
		<>
			<div
				className="d-flex align-items-center"
				style={{ gap: "8px", margin: "12px 0 32px" }}>
				<svg
					style={{ cursor: "pointer" }}
					width="24"
					height="24"
					viewBox="0 0 24 24"
					fill="none"
					xmlns="http://www.w3.org/2000/svg"
					onClick={() => navigate("/roles")}>
					<path
						d="M9.57 5.92993L3.5 11.9999L9.57 18.0699"
						stroke="#1E1F24"
						stroke-width="1.5"
						stroke-miterlimit="10"
						stroke-linecap="round"
						stroke-linejoin="round"
					/>
					<path
						d="M20.4999 12H3.66992"
						stroke="#1E1F24"
						stroke-width="1.5"
						stroke-miterlimit="10"
						stroke-linecap="round"
						stroke-linejoin="round"
					/>
				</svg>

				<label className="mb-0" style={{ fontSize: "20px", fontWeight: "600" }}>
					{!!id ? Translate[lang]?.edit : Translate[lang]?.add}{" "}
					{Translate[lang]?.role}
				</label>

				<Form.Check
					type="switch"
					id="custom-switch"
					checked={formData?.isActive}
					onChange={(e) =>
						setFormData({ ...formData, isActive: e.target.checked })
					}
				/>
			</div>
			<form onSubmit={onSubmit}>
				<div>
					<div className="rules">
						<div className="mb-4 w-50">
							<input
								type="text"
								value={formData.name}
								name="name"
								style={{
									height: "44px",
									borderRadius: "10px",
									border: "1px solid rgba(205, 206, 215, 1)",
									padding: "0 14px",
								}}
								className="w-100"
								required
								placeholder={Translate[lang]?.name}
								onChange={(e) =>
									setFormData({ ...formData, name: e.target?.value })
								}
							/>
						</div>
						<Row>
							{rolesData?.map((rul, index) => {
								return (
									<Col md={6} key={index}>
										{!!rul?.children?.length ? (
											<div>
												<div className="rul-div">
													<div className="title-bg rul-title">
														<p>
															{Translate[lang][rul?.displayName] ||
																rul?.displayName}
														</p>
														<div className="rolesFun">
															{rolesFun?.map((roleFun) => {
																return (
																	<label className="mb-0" key={roleFun}>
																		<input
																			className={
																				lang === "en" ? "mr-2" : "me-2"
																			}
																			type="checkbox"
																			name={`${roleFun}_${rul?.name}`}
																			checked={rul[roleFun]}
																			onChange={(e) => {
																				setRolesData((prev) =>
																					prev?.map((obj) =>
																						obj.id === rul?.id
																							? {
																									...obj,
																									[roleFun]: e.target?.checked,
																									children: obj?.children?.map(
																										(res) => ({
																											...res,
																											[roleFun]:
																												e.target?.checked,
																										})
																									),
																							  }
																							: obj
																					)
																				);
																			}}
																		/>{" "}
																		{Translate[lang][roleFun]}
																	</label>
																);
															})}
														</div>
													</div>
													<div className="rul-details">
														{rul?.children?.map((cont) => {
															return (
																<div className="rul-title">
																	<p>
																		{Translate[lang][cont?.displayName] ||
																			cont?.displayName}
																	</p>
																	<div className="rolesFun">
																		{rolesFun?.map((roleFun) => {
																			return (
																				<label className="mb-0" key={roleFun}>
																					<input
																						className={
																							lang === "en" ? "mr-2" : "me-2"
																						}
																						type="checkbox"
																						checked={cont[roleFun]}
																						name={`${roleFun}_${rul?.name}`}
																						onChange={(e) => {
																							setRolesData((prev) =>
																								prev?.map((obj) =>
																									obj.id === rul?.id
																										? {
																												...obj,
																												[roleFun]: false,
																												children:
																													obj?.children?.map(
																														(res) =>
																															res?.id ===
																															cont?.id
																																? {
																																		...res,
																																		[roleFun]:
																																			e.target
																																				?.checked,
																																  }
																																: res
																													),
																										  }
																										: obj
																								)
																							);
																						}}
																					/>{" "}
																					{Translate[lang][roleFun]}
																				</label>
																			);
																		})}
																	</div>
																</div>
															);
														})}
													</div>
												</div>
											</div>
										) : (
											<div>
												<div className="rul-div">
													<div className="title-bg rul-title">
														<p>{Translate[lang][rul?.displayName]}</p>
														<div className="rolesFun">
															{rolesFun?.map((roleFun) => {
																return (
																	<label className="mb-0" key={roleFun}>
																		<input
																			className={
																				lang === "en" ? "mr-2" : "me-2"
																			}
																			type="checkbox"
																			name={`${roleFun}_${rul?.text}`}
																			checked={rul[roleFun]}
																			onChange={(e) => {
																				setRolesData((prev) =>
																					prev?.map((obj) =>
																						obj.id === rul?.id
																							? {
																									...obj,
																									[roleFun]: e.target?.checked,
																							  }
																							: obj
																					)
																				);
																			}}
																		/>{" "}
																		{Translate[lang][roleFun]}
																	</label>
																);
															})}
														</div>
													</div>
												</div>
											</div>
										)}
									</Col>
								);
							})}
						</Row>
						<Row>
							<div className="col-md-6 col-12 mt-3 mb-5">
								<Button className="w-100" variant="primary" type="submit">
									{Translate[lang]?.save}
								</Button>
							</div>
						</Row>
						{/* {isExist("edit_rules") && ( */}
					</div>
				</div>
			</form>
		</>
	);
};

export default AddPermission;
