import React from "react";

import { Link, useNavigate } from "react-router-dom";
/// Image
import { Dropdown } from "react-bootstrap";
import Logout from "../../pages/Authintication/Logout";
import { useDispatch, useSelector } from "react-redux";
import { setLang, setSearch } from "../../../store/actions/AuthActions";
import { Translate } from "../../Enums/Tranlate";
import usa from "../../../images/usa.svg";
import kwait from "../../../images/kwait.svg";

const Header = ({ onNote }) => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const lang = useSelector((state) => state.auth.lang);
  const search = useSelector((state) => state.auth.search);
  const admin = useSelector((state) => state.auth.auth.admin);
  var path = window.location.pathname.split("/");
  var name = path[path.length - 1].split("-");
  var filterName = name.length >= 3 ? name.filter((n, i) => i > 0) : name;
  var finalName = filterName;

  return (
    <div className="header">
      <div className="header-content">
        <nav className="navbar navbar-expand">
          <div className="collapse navbar-collapse justify-content-between">
            <div className="header-left">
              {/* <div
                className="dashboard_bar d-flex"
                style={{ textTransform: "capitalize", alignItems: "center" }}
              >
                {!finalName?.join("_") ? Translate[lang].dashboard : Translate[lang][finalName.join("_")]}
              </div> */}
              <div className="input-group">
                <input
                  type="text"
                  className="form-control"
                  placeholder={`${Translate[lang]?.search}`}
                  value={search}
                  onChange={(e) => dispatch(setSearch(e.target.value))}
                />
                <div
                  className="flaticon-381-search-2"
                  style={{
                    position: "absolute",
                    zIndex: "3",
                    left: lang === "en" && "16px",
                    right: lang === "ar" && "16px",
                    top: "50%",
                    transform: "translate(0, -50%)",
                  }}
                ></div>
              </div>
            </div>
            <ul className="navbar-nav header-right p-0">
              <Dropdown
                as="li"
                className="nav-item dropdown notification_dropdown "
              >
                <Dropdown.Toggle
                  variant=""
                  className="nav-link ai-icon"
                  href="#"
                  role="button"
                  style={{
                    background: "none",
                  }}
                  data-toggle="dropdown"
                >
                  <img
                    src={lang === "en" ? usa : kwait}
                    width={25}
                    alt="language"
                  />{" "}
                  <i
                    className="la la-angle-down mx-2"
                    style={{ fontSize: "16px" }}
                  ></i>
                </Dropdown.Toggle>

                <Dropdown.Menu align="right" className="mt-2 custom-dropdown">
                  <ul className="timeline text-center">
                    <li
                      className="py-2"
                      style={{
                        backgroundColor:
                          lang === "en" ? "var(--bg-primary)" : "#fff",
                        color: lang === "en" ? "var(--primary)" : "#000",
                        cursor: "pointer",
                      }}
                      onClick={() => dispatch(setLang("en"))}
                    >
                      English
                    </li>
                    <li
                      className="py-2"
                      style={{
                        backgroundColor:
                          lang === "ar" ? "var(--bg-primary)" : "#fff",
                        color: lang === "ar" ? "var(--primary)" : "#000",
                        cursor: "pointer",
                      }}
                      onClick={() => dispatch(setLang("ar"))}
                    >
                      اللغه العربيه
                    </li>
                  </ul>
                </Dropdown.Menu>
              </Dropdown>

              <Dropdown as="li" className="nav-item dropdown p-0 header-profile">
                <Dropdown.Toggle
                  variant=""
                  as="a"
                  className="nav-link i-false"
                  style={{
                    background: "none",
                    cursor: "pointer",
                  }}
                >
                 {!!admin?.avatar ? <img
                    src={admin?.avatar}
                    width={40}
                    alt="user"
                    className="mx-2" 
                    style={{
                      borderRadius: '50%'
                    }}
                  /> : <i 
                    className="la la-user mx-2" 
                    style={{
                      fontSize: '32px', color: 'var(--primary)',
                      border: '1px solid var(--primary)', borderRadius: '50%'
                    }}
                    >
                  </i>}
                  <span style={{fontSize: '14px'}}>{admin?.f_name}</span>
                  <i
                    className="la la-angle-down mx-2"
                    style={{ fontSize: "16px" }}
                  ></i>
                </Dropdown.Toggle>

                <Dropdown.Menu align="right" className="mt-2 custom-dropdown">
                  <Link to="/profile" className="dropdown-item ai-icon">
                    <svg
                      id="icon-user1"
                      xmlns="http://www.w3.org/2000/svg"
                      className="text-primary"
                      width={18}
                      height={18}
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth={2}
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    >
                      <path d="M20 21v-2a4 4 0 0 0-4-4H8a4 4 0 0 0-4 4v2" />
                      <circle cx={12} cy={7} r={4} />
                    </svg>
                    <span className={lang === "en" ? "ml-2" : "mr-2"}>
                      {Translate[lang]?.profile}{" "}
                    </span>
                  </Link>
                  <Logout />
                </Dropdown.Menu>
              </Dropdown>
            </ul>
          </div>
        </nav>
      </div>
    </div>
  );
};

export default Header;
