import React, { Fragment, useEffect } from "react";
import { useState } from "react";
import { Row, Col, Card, Button } from "react-bootstrap";
import { useSelector } from "react-redux";
import { Translate } from "../../Enums/Tranlate";
import "./style.scss";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import PlatformSocialService from "../../../services/PlatformSocialService";

const PlatformSocial = () => {
	const [formData, setFormData] = useState({
		whatsapp: "",
		instagram: "",
		facebook: "",
		gmail: "",
		app_store: "",
		play_store: "",
	});
	const [loading, setLoading] = useState(false);
	const lang = useSelector((state) => state.auth?.lang);
	const Auth = useSelector((state) => state.auth?.auth);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const platformSocialService = new PlatformSocialService();

	useEffect(() => {
		platformSocialService?.getList().then((res) => {
			if (res?.status) {
				let info = res?.data?.data;
				setFormData({
					whatsapp: info?.whatsapp,
					instagram: info?.instagram,
					facebook: info?.facebook,
					gmail: info?.gmailLink,
					app_store: info?.appStore,
					play_store: info?.playStore,
				});
			}
		});
	}, []);

	const submit = (e) => {
		e.preventDefault();

		let data = {};
		if (!!formData?.facebook) data["facebook"] = formData?.facebook;
		if (!!formData?.instagram) data["instagram"] = formData?.instagram;
		if (!!formData?.whatsapp) data["whatsapp"] = formData?.whatsapp;
		if (!!formData?.gmail) data["gmailLink"] = formData?.gmail;
		if (!!formData?.app_store) data["appStore"] = formData?.app_store;
		if (!!formData?.play_store) data["playStore"] = formData?.play_store;

		platformSocialService.create(data).then((res) => {
			if (res?.status) {
				toast.success(res?.data?.message);
			}
		});
	};

	return (
		<div className="platform">
			<div className="mb-4">
				<div className="search-card d-flex justify-content-between align-items-center">
					<div>
						<span
							style={{
								fontWeight: "600",
								textTransform: "capitalize",
								color: "#000",
							}}>
							{Translate[lang].platform_social_links}
						</span>
					</div>
					<div></div>
				</div>
			</div>

			<Row>
				<Col lg={12}>
					<Card>
						<Row className="add-organization">
							<Col md={10}>
								<AvForm className="form-horizontal" onValidSubmit={submit}>
									<Row>
										<Col md={6}>
											<AvField
												label={`${Translate[lang]?.whatsapp_link}`}
												type="text"
												placeholder={Translate[lang]?.link}
												bsSize="lg"
												name="whatsapp"
												// validate={{
												//     required: {
												//         value: true,
												//         errorMessage: Translate[lang].field_required
												//     }
												// }}
												value={formData?.whatsapp}
												onChange={(e) =>
													setFormData({ ...formData, whatsapp: e.target.value })
												}
											/>
										</Col>
										<Col md={6}>
											<AvField
												label={`${Translate[lang]?.instagram_link}`}
												type="text"
												placeholder={Translate[lang]?.link}
												bsSize="lg"
												name="instagram"
												// validate={{
												//     required: {
												//         value: true,
												//         errorMessage: Translate[lang].field_required
												//     }
												// }}
												value={formData?.instagram}
												onChange={(e) =>
													setFormData({
														...formData,
														instagram: e.target.value,
													})
												}
											/>
										</Col>
										<Col md={6}>
											<AvField
												label={`${Translate[lang]?.facebook_link}`}
												type="text"
												placeholder={Translate[lang]?.link}
												bsSize="lg"
												name="facebook"
												// validate={{
												//     required: {
												//         value: true,
												//         errorMessage: Translate[lang].field_required
												//     }
												// }}
												value={formData?.facebook}
												onChange={(e) =>
													setFormData({ ...formData, facebook: e.target.value })
												}
											/>
										</Col>
										<Col md={6}>
											<AvField
												label={`${Translate[lang]?.gmail}`}
												type="email"
												placeholder={Translate[lang]?.gmail}
												bsSize="lg"
												name="gmail"
												// validate={{
												//     required: {
												//         value: true,
												//         errorMessage: Translate[lang].field_required
												//     }
												// }}
												value={formData?.gmail}
												onChange={(e) =>
													setFormData({ ...formData, gmail: e.target.value })
												}
											/>
										</Col>
										<Col md={6}>
											<AvField
												label={`${Translate[lang]?.iOS_download_link}`}
												type="text"
												placeholder={Translate[lang]?.link}
												bsSize="lg"
												name="app_store"
												// validate={{
												//     required: {
												//         value: true,
												//         errorMessage: Translate[lang].field_required
												//     }
												// }}
												value={formData?.app_store}
												onChange={(e) =>
													setFormData({
														...formData,
														app_store: e.target.value,
													})
												}
											/>
										</Col>
										<Col md={6}>
											<AvField
												label={`${Translate[lang]?.android_download_link}`}
												type="text"
												placeholder={Translate[lang]?.link}
												bsSize="lg"
												name="play_store"
												// validate={{
												//     required: {
												//         value: true,
												//         errorMessage: Translate[lang].field_required
												//     }
												// }}
												value={formData?.play_store}
												onChange={(e) =>
													setFormData({
														...formData,
														play_store: e.target.value,
													})
												}
											/>
										</Col>
										<Col md={6}>
											<Button
												variant="primary"
												type="submit"
												className="w-100 mt-3"
												disabled={loading}>
												{Translate[lang]?.save}
											</Button>
										</Col>
									</Row>
								</AvForm>
							</Col>
						</Row>
					</Card>
				</Col>
			</Row>
		</div>
	);
};

export default PlatformSocial;
