import BaseService from "./BaseService";
import http from "./HttpService";
import { API_BASE_URL_ENV } from "../jsx/common/common";

const apiEndpoint = API_BASE_URL_ENV() + "/admin/cms";

export default class StaticPagesService extends BaseService {
	constructor() {
		super(apiEndpoint);
	}

	create(type, data) {
		const body = { ...data };
		return http.post(`${apiEndpoint}/${type}`, body);
	}

	getList(type) {
		return http.get(`${apiEndpoint}/${type}`);
	}
}
