import { useEffect, useState } from "react";
import { Translate } from "../../../Enums/Tranlate";
import Loader from "../../../common/Loader";
import { useSelector } from "react-redux";
import BaseService from "../../../../services/BaseService";
import "./style.scss";
import { Card } from "react-bootstrap";
import Profile from "./Profile";
import Benefits from "./Benefits";
import Allowances from "./Allowances";
import { useLocation } from "react-router-dom";
import EmployeesService from "../../../../services/EmployeesService";
import { toast } from "react-toastify";
import Login from "./Login";

const EmployeeProfile = () => {
	const tabs = ["profile", "benefits", "allowances", "login_details"];
	const [data, setData] = useState({});
	const [loading, setLoading] = useState(false);
	const [loadingProfile, setLoadingProfile] = useState(false);
	const [selectTab, setSelectTab] = useState("profile");
	const employeesService = new EmployeesService();
	const lang = useSelector((state) => state.auth.lang);
	const location = useLocation();

	const fileHandler = (e) => {
		if (e.target.files?.length === 0) {
			return;
		}
		let files = e.target.files;
		const filesData = Object.values(files);
		setLoading(true);
		if (filesData?.length) {
			new BaseService()
				.postUpload(filesData[0])
				.then((res) => {
					if (res?.status === 200) {
						let avatar = res?.data?.url;
						employeesService
							.updateAvatar(location?.state?.id, { avatar: avatar })
							.then((res2) => {
								if (res2?.status) {
									setData({ ...data, avatar: res?.data?.url });
									toast.success(Translate[lang].updated_successfully);
								}
							});
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	useEffect(() => {
		let id = location?.state?.id;
		setLoadingProfile(true);
		employeesService
			.profile(id)
			.then((res) => {
				if (res?.status) {
					setData(res?.data?.data);
				}
				setLoadingProfile(false);
			})
			.catch((e) => setLoadingProfile(false));
	}, []);

	if (loadingProfile) {
		return <Loader />;
	}

	return (
		<div className="emp-profile">
			<div className="tabs-div">
				{tabs?.map((tab, index) => {
					return (
						<p
							key={index}
							className={`${tab === selectTab ? "activeP" : ""}`}
							onClick={() => setSelectTab(tab)}>
							{Translate[lang][tab]}
						</p>
					);
				})}
			</div>

			{selectTab === "profile" && <Profile data={data} />}
			{selectTab === "benefits" && <Benefits employee={data} />}
			{selectTab === "allowances" && <Allowances employee={data} />}
			{selectTab === "login_details" && <Login employee={data} />}
		</div>
	);
};
export default EmployeeProfile;
