import { useState } from "react";
import { Badge, Dropdown } from "react-bootstrap";
import { useSelector } from "react-redux";
import DepartmentService from "../../../../services/DepartmentService";
import DeleteModal from "../../../common/DeleteModal";
import { Translate } from "../../../Enums/Tranlate";
import editIcon from "../../../../images/icons/edit.svg";
import trashIcon from "../../../../images/icons/delete.svg";
import viewIcon from "../../../../images/icons/eye.svg";
import { useNavigate } from "react-router-dom";

const CardItem = ({
	item,
	setItem,
	index,
	setAddModal,
	type,
	setShouldUpdate,
}) => {
	const [deleteModal, setDeleteModal] = useState(false);
	const Auth = useSelector((state) => state.auth?.auth);
	const lang = useSelector((state) => state.auth?.lang);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const departmentService = new DepartmentService();
	const navigate = useNavigate();
	return (
		<tr key={index} className="text-center">
			{type === "categories" && <td>{item?.category}</td>}
			{type === "programs" && <td>{item?.program_name}</td>}
			<td>{item.subjects?.join(", ")}</td>
			{type !== "categories" && <td>{item?.age_group}</td>}
			<td>
				<div className="d-flex" style={{ gap: "20px" }}>
					{type === "programs" && (
						<img
							onClick={() => navigate(`/entertainment/${item?.id}`)}
							src={viewIcon}
							alt="viewIcon"
							className="cursor-pointer"
							width={24}
						/>
					)}
					<img
						src={editIcon}
						onClick={() => {
							if (type === "programs") {
								navigate(`/entertainment/edit-program`, { state: item });
								return;
							} else {
								setItem(item);
								setAddModal(true);
							}
						}}
						alt="edit"
						className="cursor-pointer"
						width={24}
					/>
					<img
						src={trashIcon}
						className="cursor-pointer"
						onClick={() => setDeleteModal(true)}
						alt="trash"
						width={24}
					/>
				</div>
			</td>
			{deleteModal && (
				<DeleteModal
					open={deleteModal}
					titleMsg={
						type === "categories"
							? item?.category
							: type === "programs"
							? item?.program_name
							: ""
					}
					deletedItem={item}
					modelService={departmentService}
					onCloseModal={setDeleteModal}
					setShouldUpdate={setShouldUpdate}
				/>
			)}
		</tr>
	);
};
export default CardItem;
