import React, { Fragment } from "react";
import { useState } from "react";
import { Row, Col, Card, Table, Button } from "react-bootstrap";
import { useSelector } from "react-redux";
import Loader from "../../../common/Loader";
import NoData from "../../../common/NoData";
import Pagination from "../../../common/Pagination/Pagination";
import { Translate } from "../../../Enums/Tranlate";
import CardItem from "./CardItem";
import DeductionService from "../../../../services/DeductionService";
import AddDeductionModal from "./AddDeductionModal";
import MonthDropDown, { months } from "../../../Enums/MonthDropDown";
import YearDropDown from "../../../Enums/YearDropDown";
import print from "../../../Enums/Print";
import "./style.scss";

const Deduction = () => {
	const [data, setData] = useState([]);
	const lang = useSelector((state) => state.auth?.lang);
	const [params, setParams] = useState({
		month: {
			label: Translate[lang][months[new Date().getMonth()].toLocaleLowerCase()],
			value: months[new Date().getMonth()],
		},
		year: {
			label: `${new Date().getFullYear()}`,
			value: new Date().getFullYear(),
		},
	});
	const [modal, setModal] = useState(false);
	const [item, setItem] = useState({});
	const [hasData, setHasData] = useState(null);
	const [search, setSearch] = useState(null);
	const [loading, setLoading] = useState(false);
	const [shouldUpdate, setShouldUpdate] = useState(false);
	const Auth = useSelector((state) => state.auth?.auth);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const deductionService = new DeductionService();

	const changeParams = (e, name) => {
		setParams({ ...params, [name]: e });
		setShouldUpdate((prev) => !prev);
	};

	const printProjects = () => {
		setLoading(true);
		deductionService
			.getList({
				month: params.month.value,
				year: params.year.value,
			})
			.then((res) => {
				if (res?.status === 200) {
					print(
						Translate[lang]?.deduction,
						[
							"id",
							Translate[lang]?.employee_name,
							Translate[lang]?.deduction,
							Translate[lang]?.type,
						],
						lang,
						res?.data?.data?.data.map((item) => {
							return {
								id: item.id,
								name: item.employee.name,
								salary: item.deduction,
								type: item.type,
							};
						})
					);
				}
				setLoading(false);
			})
			.catch(() => setLoading(false));
	};
	return (
		<Fragment>
			<div className="mb-2 d-flex justify-content-between align-items-center">
				<div>
					<span
						style={{
							fontWeight: "600",
							textTransform: "capitalize",
							color: "#000",
						}}>
						{Translate[lang].deduction}
					</span>
				</div>
				<div>
					{/*<Button variant="secondary" className='mx-2 h-75' onClick={printProjects}>
                {Translate[lang]?.print}
            </Button>*/}
					{
						<Button
							variant="primary"
							className="h-75"
							onClick={() => {
								setItem({});
								setModal(true);
							}}>
							{Translate[lang]?.add} {Translate[lang]?.deduction}
						</Button>
					}
				</div>
			</div>
			<Row className="mb-3">
				<Col md={3} sm={5}>
					<MonthDropDown params={params} changeParams={changeParams} />
				</Col>
				<Col md={3} sm={5}>
					<YearDropDown params={params} changeParams={changeParams} />
				</Col>
			</Row>
			<Row>
				<Col lg={12}>
					<Card>
						<div
							className={`${hasData === 0 ? "text-center" : "table-style"} `}>
							{loading && (
								<div style={{ height: "300px" }}>
									<Loader />
								</div>
							)}
							{hasData === 1 && !loading && (
								<Table responsive>
									<thead>
										<tr className="text-center">
											<th>
												<strong>{Translate[lang]?.employee_name}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.employee_id}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.deduction}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.reason}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.type}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.amount}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.action}</strong>
											</th>
										</tr>
									</thead>

									<tbody className="table-body">
										{data?.map((item, index) => {
											return (
												<CardItem
													index={index}
													key={index}
													item={item}
													setItem={setItem}
													setAddModal={setModal}
													setShouldUpdate={setShouldUpdate}
												/>
											);
										})}
									</tbody>
								</Table>
							)}
							{hasData === 0 && (
								<div className="text-center">
									<NoData />
								</div>
							)}
							<Pagination
								setData={setData}
								service={deductionService}
								shouldUpdate={shouldUpdate}
								setHasData={setHasData}
								setLoading={setLoading}
								search={search}
								param={{
									month: params.month.value,
									year: params.year.value,
								}}
							/>
						</div>
					</Card>
				</Col>
			</Row>

			{modal && (
				<AddDeductionModal
					modal={modal}
					item={item}
					setShouldUpdate={setShouldUpdate}
					setModal={() => setModal(false)}
				/>
			)}
		</Fragment>
	);
};

export default Deduction;
