import React from "react";
import { useState } from "react";
import { Row, Col, Card, Table } from "react-bootstrap";
import { useSelector } from "react-redux";
import Loader from "../../common/Loader";
import NoData from "../../common/NoData";
import Pagination from "../../common/Pagination/Pagination";
import { Translate } from "../../Enums/Tranlate";
import CardItem from "./CardItem";
import "./style.scss";
import JoinService from "../../../services/JoinService";
import RequestsService from "../../../services/RequestsService";

const Requests = () => {
	const [data, setData] = useState([]);
	const [hasData, setHasData] = useState(null);
	const tabs = ["job_application", "complaint", "suggestion", "inquiry"];
	const [type, setType] = useState("job_application");
	const [loading, setLoading] = useState(false);
	const [shouldUpdate, setShouldUpdate] = useState(false);
	const lang = useSelector((state) => state.auth?.lang);
	const Auth = useSelector((state) => state.auth?.auth);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const requestsService = new RequestsService();
	const joinService = new JoinService();

	return (
		<div className="department">
			<div className="tabs-div">
				{tabs?.map((tab) => {
					return (
						<p
							key={tab}
							onClick={() => {
								setType(tab);
								setShouldUpdate((prev) => !prev);
							}}
							className={type === tab ? "activeP" : ""}>
							{Translate[lang][tab]}
						</p>
					);
				})}
			</div>
			<div className="mb-4">
				<div className="search-card d-flex justify-content-between align-items-center">
					<div>
						<span
							style={{
								fontWeight: "600",
								textTransform: "capitalize",
								color: "#000",
							}}>
							{Translate[lang][type]}
						</span>
					</div>
					<div></div>
				</div>
			</div>

			<Row>
				<Col lg={12}>
					<Card>
						<div
							className={`${hasData === 0 ? "text-center" : "table-style"} `}>
							{loading && (
								<div style={{ height: "400px" }}>
									<Loader />
								</div>
							)}

							{hasData === 1 && !loading && (
								<Table responsive>
									<thead>
										<tr className="text-center">
											{type === "job_application" && (
												<th>
													<strong>{Translate[lang]?.name}</strong>
												</th>
											)}
											{type === "job_application" && (
												<th>
													<strong>{Translate[lang]?.job_title}</strong>
												</th>
											)}
											{type === "job_application" && (
												<th>
													<strong>{Translate[lang]?.position_applied}</strong>
												</th>
											)}
											<th>
												<strong>{Translate[lang]?.phone}</strong>
											</th>
											{type === "job_application" && (
												<th>
													<strong>{Translate[lang]?.email}</strong>
												</th>
											)}
											{type === "job_application" && (
												<th>
													<strong>{Translate[lang]?.cv}</strong>
												</th>
											)}
											{type !== "job_application" && (
												<th>
													<strong>{Translate[lang]?.title}</strong>
												</th>
											)}
											{type !== "job_application" && (
												<th>
													<strong>{Translate[lang]?.subject}</strong>
												</th>
											)}
											{type !== "job_application" && (
												<th>
													<strong>{Translate[lang]?.status}</strong>
												</th>
											)}
											<th>
												<strong>{Translate[lang]?.action}</strong>
											</th>
										</tr>
									</thead>

									<tbody className="table-body">
										{data?.map((item, index) => {
											return (
												<CardItem
													index={index}
													key={index}
													item={item}
													setShouldUpdate={setShouldUpdate}
													type={type}
												/>
											);
										})}
									</tbody>
								</Table>
							)}
							{hasData === 0 && <NoData />}
							{type === "job_application" ? (
								<Pagination
									setData={setData}
									service={joinService}
									shouldUpdate={shouldUpdate}
									setHasData={setHasData}
									setLoading={setLoading}
								/>
							) : (
								<Pagination
									setData={setData}
									service={requestsService}
									shouldUpdate={shouldUpdate}
									setHasData={setHasData}
									setLoading={setLoading}
									type={type.toLocaleUpperCase()}
								/>
							)}
						</div>
					</Card>
				</Col>
			</Row>
		</div>
	);
};

export default Requests;
