import { useEffect, useState } from "react";
import { Button, Card, Table } from "react-bootstrap";
import { useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import Loader from "../../common/Loader";
import NoData from "../../common/NoData";
import Pagination from "../../common/Pagination/Pagination";
import { Translate } from "../../Enums/Tranlate";
import CardItem from "./CardItem";
import OrganizationService from "../../../services/OrganizationService";

const Organization = () => {
	const [data, setData] = useState([]);
	const [hasData, setHasData] = useState(1);
	const [shouldUpdate, setShouldUpdate] = useState(false);
	const [loading, setLoading] = useState(false);
	const navigate = useNavigate();
	const organizationService = new OrganizationService();
	const Auth = useSelector((state) => state.auth?.auth);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const lang = useSelector((state) => state.auth?.lang);

	useEffect(() => {
		setLoading(true);
		organizationService.getList().then((res) => {
			if (res?.status) {
				setData(res?.data?.data);
				if (!!res?.data?.data?.length) {
					setHasData(1);
				} else {
					setHasData(0);
				}
			}
			setLoading(false);
		});
	}, [shouldUpdate]);

	return (
		<div className="organization">
			<div className="mb-4">
				<div className="assets-div d-flex justify-content-between align-items-center">
					<div>
						<span
							style={{
								fontWeight: "600",
								textTransform: "capitalize",
								color: "#000",
							}}>
							{Translate[lang].organization}
						</span>
					</div>
					<div className="add-assets">
						{/* isExist("add_organization") &&  */}
						{
							<Link to="/organization/add" className="btn btn-primary h-75">
								<i className="la la-plus mx-1"></i>
								{Translate[lang]?.add} {Translate[lang]?.organization}
							</Link>
						}
					</div>
				</div>
			</div>
			<Card>
				<div className={`${hasData === 0 ? "text-center" : "table-style"}`}>
					{loading && (
						<div style={{ height: "300px" }}>
							<Loader />
						</div>
					)}
					{hasData === 1 && !loading && (
						<Table responsive>
							<thead>
								<tr>
									<th>
										<strong>{Translate[lang]?.name}</strong>
									</th>
									<th>
										<strong>{Translate[lang]?.civil_id}</strong>
									</th>
									<th>
										<strong>{Translate[lang]?.owner}</strong>
									</th>
									<th>
										<strong>{Translate[lang]?.phone}</strong>
									</th>
									<th className="text-center">
										<strong>{Translate[lang]?.action}</strong>
									</th>
								</tr>
							</thead>
							<tbody>
								{data?.map((item, index) => {
									return (
										<CardItem
											key={index}
											index={index}
											item={item}
											setShouldUpdate={setShouldUpdate}
										/>
									);
								})}
							</tbody>
						</Table>
					)}
					{hasData === 0 && <NoData />}
					{/* <Pagination
            setData={setData}
            service={organizationService}
            shouldUpdate={shouldUpdate}
            setHasData={setHasData}
            setLoading={setLoading}
          /> */}
				</div>
			</Card>
		</div>
	);
};
export default Organization;
