import React, { useEffect, useState } from "react";
import { Badge, Card, Col, Row, Table } from "react-bootstrap";
import { useSelector } from "react-redux";
import { Translate } from "../../Enums/Tranlate";
import AdminService from "../../../services/AdminService";
import { useNavigate } from "react-router-dom";
import dollar from "../../../images/icons/dollar.svg"
import './style.scss'

const Dashboard = () => {
  const [formData ,setFormData] = useState({
    totalCompany: '0',
    totalPeople: '0',
    totalLeave: '0',
    totalAssets: '0',
    totalAdmins: '0',
    totalResignation: '0'
  })
  const [products, setProducts] = useState([
    {name: 'T-Shirt', payment: 'cash', status: 'success', price: 10, amount: 4},
    {name: 'T-Shirt', payment: 'cash', status: 'pending', price: 15, amount: 1},
    {name: 'T-Shirt', payment: 'visa', status: 'ordered', price: 4, amount: 2},
    {name: 'T-Shirt', payment: 'knet', status: 'canceled', price: 5, amount: 3},
    {name: 'T-Shirt', payment: 'visa', status: 'delivered', price: 7, amount: 1},
  ])
  const [loading, setLoading] = useState(false)
  const lang = useSelector(state=> state.auth.lang)
  const navigate = useNavigate()

  // useEffect(()=>{
  //   setLoading(true)
  //   new AdminService().getDashboard().then(res=>{
  //     if(res?.status){
  //       setFormData(res.data.data)
  //     }
  //     setLoading(false)
  //   }).catch(()=> setLoading(false))
  // },[])

  if(loading) {
    return <Row>
      {Object.entries(formData)?.map(data=>{
        return <Col className="col-md-4 col-sm-6">
          <Card style={{height: '128.75px'}}>
            <Card.Body>
              <div class="skeleton-loader">
                  <div class="loader-header"></div>
                  <div class="loader-content"></div>
              </div>
            </Card.Body>
          </Card>
        </Col>
      })}
    </Row>
  }

  return (
      <div className="dashboard">
        <div className='dashboard-info'>
          <div className="card" onClick={()=> navigate('/')}>
            <img src={dollar} alt='dollar' width={40} className="mb-2" />
            <h2 className="fs-18 mb-2">Total Income</h2>
            <span className="fs-16">4000 KWD</span>
          </div>
          <div className="card" onClick={()=> navigate('/')}>
            <img src={dollar} alt='dollar' width={40} className="mb-2" />
            <h2 className="fs-18 mb-2">Total Income</h2>
            <span className="fs-16">4000 KWD</span>
          </div>
          <div className="card" onClick={()=> navigate('/')}>
            <img src={dollar} alt='dollar' width={40} className="mb-2" />
            <h2 className="fs-18 mb-2">Total Income</h2>
            <span className="fs-16">4000 KWD</span>
          </div>
          <div className="card" onClick={()=> navigate('/')}>
            <img src={dollar} alt='dollar' width={40} className="mb-2" />
            <h2 className="fs-18 mb-2">Total Income</h2>
            <span className="fs-16">4000 KWD</span>
          </div>
          <div className="card" onClick={()=> navigate('/')}>
            <img src={dollar} alt='dollar' width={40} className="mb-2" />
            <h2 className="fs-18 mb-2">Total Income</h2>
            <span className="fs-16">4000 KWD</span>
          </div>
        </div>
        <Row>
          <Col md={9} className="recent-transactions">
            <div className="dashboard-products card">
              <p className="card-title">{Translate[lang].recent_transactions}</p>
              <div className='cart-body'>
                <Table responsive>
                  <thead>
                    <tr className='text-center'>
                      <th>
                        <strong>{Translate[lang]?.orders}</strong>
                      </th>
                      <th>
                        <strong>{Translate[lang]?.payment}</strong>
                      </th>
                      <th>
                        <strong>{Translate[lang]?.status}</strong>
                      </th>
                      <th>
                        <strong>{Translate[lang]?.amount}</strong>
                      </th>
                      <th>
                        <strong>{Translate[lang]?.price}</strong>
                      </th>
                    </tr>
                  </thead>
                  <tbody className="table-body">
                    {products?.map((product, index) => {
                      return <tr key={index} className='text-center'>
                        <td>
                          <div className="d-flex" style={{gap: '8px', alignItems: 'center'}}>
                            <i 
                              className="la la-tshirt"
                              style={{fontSize: '3rem'}}
                            ></i>
                            <span>{product?.name}</span>
                          </div>
                        </td>
                        <td>
                            {Translate[lang][product?.payment]}
                        </td>
                        <td>
                          <Badge
                            className="text-capitalize"
                            variant={`${
                              product.status === "delivered"
                                ? "success"
                                : product.status === "canceled"
                                ? "danger"
                                : product.status === "ordered"
                                ? "primary"
                                : product.status === "pending"
                                ? "warning"
                                : product.status === "success"
                                ? "info"
                                : ""
                            } light`}
                          >
                            {product?.status}
                          </Badge>
                        </td>
                        <td>{product?.amount}</td>
                        <td>{product?.price}</td>
                      </tr>
                    })}
                  </tbody>
                </Table>
              </div>
            </div>
          </Col>
          <Col md={3} className='best-seller' style={{
            paddingLeft: lang === 'en' ? '6px' : '',
            paddingRight: lang === 'ar' ? '6px' : '',
          }}>
            <div className="dashboard-products card">
              <p className="card-title">{Translate[lang].best_seller}</p>

              <div>
              {products?.map((product, index) => {
                      return <div key={index} className='d-flex justify-content-between mb-3'>
                        <div className="d-flex" style={{gap: '8px', alignItems: 'center'}}>
                            <i 
                              className="la la-tshirt p-2"
                              style={{
                                fontSize: '2.5rem',
                                border: '1px solid #dedede',
                                borderRadius: '5px'
                              }}
                            ></i>
                            <div>
                              <p className="mb-2" style={{lineHeight: '1', fontWeight: '600', fontSize: '18px'}}>{product?.name}</p>
                              <p className="m-0" style={{lineHeight: '1'}}>{product?.price.toFixed(3)}</p>
                            </div>
                        </div>
                        <div className="d-flex flex-column justify-content-around">
                          <p className="mb-2" style={{lineHeight: '1'}}>Sales</p>
                          <p className="m-0" style={{lineHeight: '1'}}>2000</p>
                        </div>
                      </div>
                    })}
              </div>
            </div>
          </Col>
        </Row>
      </div>
  );
};

export default Dashboard;
