import React, { Fragment, useEffect } from "react";
import { useState } from "react";
import { Row, Col, Card, Table, Button } from "react-bootstrap";
import { useSelector } from "react-redux";
import Loader from "../../common/Loader";
import NoData from "../../common/NoData";
import Pagination from "../../common/Pagination/Pagination";
import { Translate } from "../../Enums/Tranlate";
import CardItem from "./CardItem";
import AddDepartmentsModal from "./AddDepartmentsModal";
import DepartmentService from "../../../services/DepartmentService";
import ViewModal from "./ViewModal";
import "./style.scss";

const Departments = () => {
	const [data, setData] = useState([]);
	const [assign, setAssign] = useState(false);
	const [addModal, setAddModal] = useState(false);
	const [viewModal, setViewModal] = useState(false);
	const [item, setItem] = useState({});
	const [hasData, setHasData] = useState(null);
	const [type, setType] = useState("departments");
	const [loading, setLoading] = useState(false);
	const [shouldUpdate, setShouldUpdate] = useState(false);
	const lang = useSelector((state) => state.auth?.lang);
	const departmentService = new DepartmentService();
	const Auth = useSelector((state) => state.auth?.auth);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);

	useEffect(() => {
		if (type === "departments") {
			setLoading(true);
			departmentService.getList().then((res) => {
				if (res?.status) {
					setData(res?.data?.data);
					if (!!res?.data?.data?.length) {
						setHasData(1);
					} else {
						setHasData(0);
					}
				}
				setLoading(false);
			});
		}
	}, [shouldUpdate, type]);

	return (
		<div className="department">
			<div className="tabs-div">
				<p
					onClick={() => setType("departments")}
					className={type === "departments" ? "activeP" : ""}>
					{Translate[lang].departments}
				</p>
				<p
					onClick={() => setType("units")}
					className={type === "units" ? "activeP" : ""}>
					{Translate[lang].units}
				</p>
			</div>
			<div className="mb-4">
				<div className="search-card d-flex justify-content-between align-items-center">
					<div>
						<span
							style={{
								fontWeight: "600",
								textTransform: "capitalize",
								color: "#000",
							}}>
							{Translate[lang][type]}
						</span>
					</div>
					<div className="add-department">
						{
							<Button
								variant="primary"
								className="me-2 h-75"
								onClick={() => {
									setItem({});
									setAddModal(true);
									setAssign(false);
								}}>
								<i className="la la-plus mx-1"></i>
								{Translate[lang]?.add}{" "}
								{type === "departments"
									? Translate[lang]?.department
									: Translate[lang]?.unit}
							</Button>
						}
					</div>
				</div>
			</div>

			<Row>
				<Col lg={12}>
					<Card>
						<div
							className={`${hasData === 0 ? "text-center" : "table-style"} `}>
							{loading && (
								<div style={{ height: "400px" }}>
									<Loader />
								</div>
							)}

							{hasData === 1 && !loading && (
								<Table responsive>
									<thead>
										<tr className="text-center">
											<th>
												<strong>
													{type === "departments"
														? Translate[lang]?.department_name
														: Translate[lang]?.unit_name}
												</strong>
											</th>
											<th>
												<strong>
													{type === "departments"
														? Translate[lang]?.department_manager
														: Translate[lang]?.department}
												</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.organization}</strong>
											</th>
											<th>
												<strong>
													{type === "departments"
														? Translate[lang]?.no_of_units
														: Translate[lang]?.unit_manager}
												</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.no_of_employees}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.status}</strong>
											</th>
											<th>
												<strong>{Translate[lang]?.action}</strong>
											</th>
										</tr>
									</thead>

									<tbody className="table-body">
										{data?.map((item, index) => {
											return (
												<CardItem
													index={index}
													key={index}
													item={item}
													setItem={setItem}
													setAddModal={setAddModal}
													setShouldUpdate={setShouldUpdate}
													setViewModal={setViewModal}
													type={type}
													setAssign={setAssign}
												/>
											);
										})}
									</tbody>
								</Table>
							)}
							{hasData === 0 && <NoData />}
							{/* <Pagination
								setData={setData}
								service={departmentService}
								shouldUpdate={shouldUpdate}
								setHasData={setHasData}
								setLoading={setLoading}
							/> */}
						</div>
					</Card>
				</Col>
			</Row>

			{addModal && (
				<AddDepartmentsModal
					item={item}
					addModal={addModal}
					setShouldUpdate={setShouldUpdate}
					setAddModal={() => setAddModal(false)}
					type={type}
					Service={departmentService}
					assign={assign}
				/>
			)}

			{viewModal && (
				<ViewModal
					viewModal={viewModal}
					setViewModal={setViewModal}
					item={item}
				/>
			)}
		</div>
	);
};

export default Departments;
