import React, { Fragment, useEffect } from "react";
import { useState } from "react";
import { useSelector } from "react-redux";
import { Translate } from "../../Enums/Tranlate";
import ActiveEmployees from "./Active";
import Resignation from "./Resignation";
import NoticePeriod from "./NoticePeriod";
import DeletedEmployees from "./Deleted";
import Deduction from "./Deduction";
import Provision from "./Provision";
import { useLocation } from "react-router-dom";
import "./style.scss";

const tabs = [
	"employees",
	"notice_period",
	"resignation",
	"termination",
	"deduction",
	"provision",
	"indemnity",
];

const Employees = () => {
	const [selectTab, setSelectTab] = useState("employees");
	const lang = useSelector((state) => state.auth?.lang);
	const Auth = useSelector((state) => state.auth?.auth);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const location = useLocation();

	useEffect(() => {
		if (!!location?.state) {
			if (location?.state === "resignation") setSelectTab("resignation");
		}
	}, [location]);

	return (
		<Fragment>
			<div className="mb-3">
				<div className="tabs-div">
					{tabs?.map((tab, index) => {
						return (
							<p
								key={index}
								className={`${tab === selectTab ? "activeP" : ""}`}
								onClick={() => setSelectTab(tab)}>
								{Translate[lang][tab]}
							</p>
						);
					})}
				</div>
			</div>

			{selectTab === "employees" && <ActiveEmployees />}
			{selectTab === "notice_period" && <NoticePeriod />}
			{selectTab === "resignation" && <Resignation />}
			{selectTab === "termination" && <DeletedEmployees />}
			{selectTab === "deduction" && <Deduction />}
			{(selectTab === "provision" || selectTab === "indemnity") && (
				<Provision type={selectTab} />
			)}
		</Fragment>
	);
};

export default Employees;
