import React, { Fragment, useEffect } from "react";
import { useState } from "react";
import { Row, Col, Card, Table } from "react-bootstrap";
import { useSelector } from "react-redux";
import Loader from "../../../common/Loader";
import NoData from "../../../common/NoData";
import Pagination from "../../../common/Pagination/Pagination";
import { Translate } from "../../../Enums/Tranlate";
import CardItem from "./CardItem";
import "./style.scss";
import IndemnityCardItem from "./IndemnityCardItem";
import ProvisionService from "../../../../services/ProvisionService";
import IndemnityService from "../../../../services/IndemnityService";
import EndOfService from "./EndOfService";

const Provision = ({ type }) => {
	const [data, setData] = useState([]);
	const [selectTab, setSelectTab] = useState(type);
	const [hasData, setHasData] = useState(null);
	const [search, setSearch] = useState(null);
	const [loading, setLoading] = useState(false);
	const [shouldUpdate, setShouldUpdate] = useState(false);
	const lang = useSelector((state) => state.auth?.lang);

	useEffect(() => setSelectTab(type), [type]);

	return (
		<Fragment>
			<div className="mb-4 d-flex justify-content-between align-items-center">
				<div>
					<span
						style={{
							fontWeight: "600",
							textTransform: "capitalize",
							color: "#000",
						}}>
						{Translate[lang][type]}
					</span>
				</div>
			</div>

			{selectTab === "provision" && (
				<Row>
					<Col lg={12}>
						<Card>
							<div
								className={`${hasData === 0 ? "text-center" : "table-style"} `}>
								{loading && (
									<div style={{ height: "300px" }}>
										<Loader />
									</div>
								)}

								{hasData === 1 && !loading && (
									<Table responsive>
										<thead>
											<tr className="text-center">
												<th>
													<strong>I.D</strong>
												</th>
												<th>
													<strong>{Translate[lang]?.employee_name}</strong>
												</th>
												<th>
													<strong>{Translate[lang]?.job_title}</strong>
												</th>
												<th>
													<strong>{Translate[lang]?.department}</strong>
												</th>
												<th>
													<strong>{Translate[lang]?.accrued_leave}</strong>
												</th>
												<th>
													<strong>{Translate[lang]?.provision} KWD</strong>
												</th>
												<th></th>
											</tr>
										</thead>

										<tbody className="table-body">
											{data?.map((item, index) => {
												return (
													<CardItem index={index} key={index} item={item} />
												);
											})}
										</tbody>
									</Table>
								)}
								{hasData === 0 && !loading && <NoData />}
								<Pagination
									setData={setData}
									service={new ProvisionService()}
									shouldUpdate={shouldUpdate}
									setHasData={setHasData}
									setLoading={setLoading}
									search={search}
								/>
							</div>
						</Card>
					</Col>
				</Row>
			)}
			{selectTab === "indemnity" && (
				<Row>
					<Col lg={12}>
						<Card>
							<div
								className={`${hasData === 0 ? "text-center" : "table-style"} `}>
								{loading && (
									<div style={{ height: "300px" }}>
										<Loader />
									</div>
								)}

								{hasData === 1 && !loading && (
									<Table responsive>
										<thead>
											<tr className="text-center">
												<th>
													<strong>I.D</strong>
												</th>
												<th>
													<strong>{Translate[lang]?.employee_name}</strong>
												</th>
												<th>
													<strong>{Translate[lang]?.job_title}</strong>
												</th>
												<th>
													<strong>{Translate[lang]?.department}</strong>
												</th>
												<th>
													<strong>{Translate[lang]?.start_date}</strong>
												</th>
												<th>
													<strong>{Translate[lang].civil_id}</strong>
												</th>
												<th>
													<strong>{Translate[lang]?.accrued_leave}</strong>
												</th>
												<th>
													<strong>{Translate[lang]?.salary}</strong>
												</th>
												<th>
													<strong>{Translate[lang]?.daily_indemnity}</strong>
												</th>
												<th>
													<strong>
														{Translate[lang]?.exceeding_years_indemnity}
													</strong>
												</th>
												<th>
													<strong>
														{Translate[lang]?.first5YearsIndemnity}
													</strong>
												</th>
												<th>
													<strong>{Translate[lang]?.monthly_indemnity}</strong>
												</th>
												<th>
													<strong>
														{Translate[lang]?.paid_leave_balance_amount}
													</strong>
												</th>
												<th>
													<strong>{Translate[lang]?.total_indemnity}</strong>
												</th>
											</tr>
										</thead>

										<tbody className="table-body">
											{data?.map((item, index) => {
												return (
													<IndemnityCardItem
														index={index}
														key={index}
														item={item}
													/>
												);
											})}
										</tbody>
									</Table>
								)}
								{hasData === 0 && !loading && <NoData />}
								<Pagination
									setData={setData}
									service={new IndemnityService()}
									shouldUpdate={shouldUpdate}
									setHasData={setHasData}
									setLoading={setLoading}
									search={search}
								/>
							</div>
						</Card>
					</Col>
				</Row>
			)}
			{selectTab === "end_of_service" && <EndOfService />}
		</Fragment>
	);
};

export default Provision;
