import React, { useEffect, useState } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import OrganizationService from "../../../../services/OrganizationService";
import { useLocation } from "react-router-dom";
import Select from "react-select";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { useSelector } from "react-redux";
import { Translate } from "../../../Enums/Tranlate";

const AddProgram = () => {
	const location = useLocation();
	const [formData, setFormData] = useState({
		name: "",
		choose: "at_home",
		education_type: "",
		grade: "",
		program_start_date: "",
		program_end_date: "",
		subjects: "",
		program_type: "foundation",
		lessons_frequency_week: "",
		subjects_info: [
			{
				subject: "",
				grade: "",
				price: "",
			},
		],
		program_classification: "group",
		branch: "",

		hall_name: "",
		hall_no: "",
		coach: "",
		gender: "",
		time_from: "",
		time_to: "",
		number_of_classes: "",
		classes: [],
		capacity_assumption: "",
		capacity: "",
		days: "",
	});
	const [loading, setLoading] = useState(false);
	const [id, setId] = useState(null);
	const [view, setView] = useState(false);
	const navigate = useNavigate();
	const organizationService = new OrganizationService();
	const lang = useSelector((state) => state.auth?.lang);

	useEffect(() => {
		if (
			location?.pathname.includes("edit") ||
			location?.pathname.includes("view")
		) {
			let item = location.state;
			setId(item?.id);
			// setFormData({
			// })
		}
		if (location?.pathname.includes("view")) {
			setView(true);
		}
	}, [location]);

	const inputHandler = (e) => {
		setFormData({ ...formData, [e.target.name]: e.target.value });
	};

	const onSubmit = (e) => {
		e.preventDefault();

		let data = {};

		// if(!!id){
		//    setLoading(true)
		//    organizationService.update(id, data).then((response) =>{
		//       if(response?.status === 200){
		//          toast.success(Translate[lang].admin_updated_successfully)
		//          navigate('/organization')
		//       }
		//       setLoading(false)
		//    })
		// } else {
		//    setLoading(true)
		//    organizationService.create(data).then((response) =>{
		//       if(response?.status === 201){
		//          toast.success(`${Translate[lang].added} ${Translate[lang].admin} ${Translate[lang].successfully}`)
		//          navigate('/organization')
		//       }
		//       setLoading(false)
		//    })
		// }
	};

	return (
		<>
			<div
				className="d-flex  align-items-center"
				style={{ gap: "8px", margin: "12px 0 32px" }}>
				<svg
					style={{ cursor: "pointer" }}
					width="24"
					height="24"
					viewBox="0 0 24 24"
					fill="none"
					xmlns="http://www.w3.org/2000/svg"
					onClick={() => navigate("/education", { state: "programs" })}>
					<path
						d="M9.57 5.92993L3.5 11.9999L9.57 18.0699"
						stroke="#1E1F24"
						stroke-width="1.5"
						stroke-miterlimit="10"
						stroke-linecap="round"
						stroke-linejoin="round"
					/>
					<path
						d="M20.4999 12H3.66992"
						stroke="#1E1F24"
						stroke-width="1.5"
						stroke-miterlimit="10"
						stroke-linecap="round"
						stroke-linejoin="round"
					/>
				</svg>

				<label className="mb-0" style={{ fontSize: "20px", fontWeight: "600" }}>
					{view
						? Translate[lang]?.view
						: !!id
						? Translate[lang]?.edit
						: Translate[lang]?.add}{" "}
					{Translate[lang]?.program}
				</label>
			</div>
			<Row className="add-organization">
				<Col md={10}>
					<AvForm onValidSubmit={onSubmit}>
						<div className="row">
							<div className="col-lg-6 col-sm-6 ">
								<AvField
									label={`${Translate[lang]?.name}`}
									name="name"
									type="text"
									value={formData?.name}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.name}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 ">
								<label className="text-label">{Translate[lang]?.choose}</label>
								<div
									className="d-flex align-items-center"
									style={{ gap: "16px", height: "40px" }}>
									<label className="mb-0 d-flex" style={{ gap: "8px" }}>
										<input
											type="radio"
											name="at_home"
											checked={formData?.choose === "at_home"}
											onChange={() =>
												setFormData({ ...formData, choose: "at_home" })
											}
										/>
										<span>{Translate[lang].at_home}</span>
									</label>
									<label className="mb-0 d-flex" style={{ gap: "8px" }}>
										<input
											type="radio"
											name="at_center"
											checked={formData?.choose === "at_center"}
											onChange={() =>
												setFormData({ ...formData, choose: "at_center" })
											}
										/>
										<span>{Translate[lang].at_center}</span>
									</label>
									<label className="mb-0 d-flex" style={{ gap: "8px" }}>
										<input
											type="radio"
											name="online"
											checked={formData?.choose === "online"}
											onChange={() =>
												setFormData({ ...formData, choose: "online" })
											}
										/>
										<span>{Translate[lang].online}</span>
									</label>
								</div>
							</div>
							{formData?.choose === "at_center" && (
								<div className="col-lg-6 col-sm-6 mb-3">
									<label className="text-label">
										{Translate[lang]?.program_classification}
									</label>
									<div
										className="d-flex align-items-center"
										style={{ gap: "16px", height: "40px" }}>
										<label className="mb-0 d-flex" style={{ gap: "8px" }}>
											<input
												type="radio"
												name="group"
												checked={formData?.program_classification === "group"}
												onChange={() =>
													setFormData({
														...formData,
														program_classification: "group",
													})
												}
											/>
											<span>{Translate[lang].group}</span>
										</label>
										<label className="mb-0 d-flex" style={{ gap: "8px" }}>
											<input
												type="radio"
												name="private"
												checked={formData?.program_classification === "private"}
												onChange={() =>
													setFormData({
														...formData,
														program_classification: "private",
													})
												}
											/>
											<span>{Translate[lang].private}</span>
										</label>
									</div>
								</div>
							)}
							{formData?.choose === "at_center" && (
								<div className="col-lg-6 col-sm-6 "></div>
							)}
							<div className="col-lg-6 col-sm-6 mb-3">
								<label className="text-label">
									{Translate[lang]?.education_type}
								</label>
								<Select
									value={formData?.education_type}
									name="education_type"
									placeholder={Translate[lang]?.select}
									options={[]}
									onChange={(e) =>
										setFormData({ ...formData, education_type: e })
									}
								/>
							</div>
							{formData?.choose === "at_center" && (
								<div className="col-lg-6 col-sm-6 ">
									<label className="text-label">
										{Translate[lang]?.branch}
									</label>
									<Select
										value={formData?.branch}
										name="branch"
										placeholder={Translate[lang]?.select}
										options={[]}
										onChange={(e) => setFormData({ ...formData, branch: e })}
									/>
								</div>
							)}
							<div className="col-lg-6 col-sm-6 mb-3">
								<label className="text-label">
									{Translate[lang]?.grade}{" "}
									{formData?.choose === "at_center" &&
									formData?.program_classification === "private"
										? `(${Translate[lang]?.multi_select})`
										: ""}
								</label>
								<Select
									value={formData?.grade}
									name="grade"
									placeholder={Translate[lang]?.select}
									multi={
										formData?.choose === "at_center" &&
										formData?.program_classification === "private"
									}
									options={[]}
									onChange={(e) => setFormData({ ...formData, grade: e })}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 ">
								<AvField
									label={`${Translate[lang]?.program_start_date}`}
									name="program_start_date"
									type="date"
									value={formData?.program_start_date}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.program_start_date}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 ">
								<AvField
									label={`${Translate[lang]?.program_end_date}`}
									name="program_end_date"
									type="date"
									value={formData?.program_end_date}
									disabled={view}
									validate={{
										required: {
											value: true,
											errorMessage: Translate[lang].field_required,
										},
									}}
									placeholder={`${Translate[lang]?.program_end_date}`}
									onChange={(e) => inputHandler(e)}
								/>
							</div>
							<div className="col-lg-6 col-sm-6 mb-3">
								<label className="text-label">
									{Translate[lang]?.subjects} ({Translate[lang]?.multi_select})
								</label>
								<Select
									value={formData?.subjects}
									name="subjects"
									placeholder={Translate[lang]?.select}
									multi={formData?.choose !== "at_center"}
									options={[]}
									onChange={(e) => setFormData({ ...formData, subjects: e })}
								/>
							</div>
							{formData?.choose === "at_center" && (
								<div className="col-lg-6 col-sm-6 mb-3">
									<label className="text-label">{Translate[lang]?.halls}</label>
									<Select
										value={formData?.halls}
										name="halls"
										placeholder={Translate[lang]?.select}
										options={[]}
										onChange={(e) => setFormData({ ...formData, halls: e })}
									/>
								</div>
							)}
							<div className="col-lg-6 col-sm-6 mb-3"></div>
							{formData?.choose !== "online" &&
								formData?.subjects_info?.map((subject, index) => {
									return (
										<React.Fragment key={index}>
											<Col md={formData?.choose === "at_home" ? 3 : 6}>
												<label className="text-label">
													{Translate[lang]?.subject}
												</label>
												{formData?.choose === "at_home" ? (
													<Select
														className="mb-3"
														value={subject?.subject}
														name={`subject${index}`}
														placeholder={Translate[lang]?.select}
														options={[]}
														disabled={view}
														onChange={(e) => {
															let update = formData?.subjects_info?.map(
																(res, ind) => {
																	if (index === ind) {
																		return {
																			...res,
																			subject: e,
																		};
																	} else {
																		return res;
																	}
																}
															);
															setFormData({
																...formData,
																subjects_info: update,
															});
														}}
													/>
												) : (
													<AvField
														name={`subject${index}`}
														type="text"
														value={subject?.subject}
														disabled={view}
														validate={{
															required: {
																value: true,
																errorMessage: Translate[lang].field_required,
															},
														}}
														placeholder={`${Translate[lang]?.subject}`}
														onChange={(e) => {
															let update = formData?.subjects_info?.map(
																(res, ind) => {
																	if (index === ind) {
																		return {
																			...res,
																			subject: e.target.value,
																		};
																	} else {
																		return res;
																	}
																}
															);
															setFormData({
																...formData,
																subjects_info: update,
															});
														}}
													/>
												)}
											</Col>
											{formData?.choose === "at_home" && (
												<div className="col-lg-3 col-3 mb-3">
													<label className="text-label">
														{Translate[lang]?.grade}
													</label>
													<Select
														value={subject?.grade}
														name={`grade${index}`}
														placeholder={Translate[lang]?.select}
														options={[]}
														disabled={view}
														onChange={(e) => {
															let update = formData?.subjects_info?.map(
																(res, ind) => {
																	if (index === ind) {
																		return {
																			...res,
																			grade: e,
																		};
																	} else {
																		return res;
																	}
																}
															);
															setFormData({
																...formData,
																subjects_info: update,
															});
														}}
													/>
												</div>
											)}
											<div
												className="col-lg-6 col-sm-6 d-flex align-items-center"
												style={{ gap: "12px" }}>
												<div className="w-100">
													<AvField
														label={
															formData?.choose === "at_home"
																? `${Translate[lang]?.price}/${Translate[lang]?.class}`
																: Translate[lang]?.price
														}
														name={`price${index}`}
														type="number"
														value={subject?.price}
														disabled={view}
														validate={{
															required: {
																value: true,
																errorMessage: Translate[lang].field_required,
															},
														}}
														placeholder={`${Translate[lang]?.price}`}
														onChange={(e) => {
															let update = formData?.subjects_info?.map(
																(res, ind) => {
																	if (index === ind) {
																		return {
																			...res,
																			price: e.target.value,
																		};
																	} else {
																		return res;
																	}
																}
															);
															setFormData({
																...formData,
																subjects_info: update,
															});
														}}
													/>
												</div>
												{formData?.choose === "at_home" &&
													formData?.subjects_info?.length - 1 === index && (
														<i
															className="la la-plus-circle cursor-pointer mt-1"
															style={{ fontSize: "2.5rem" }}
															onClick={() =>
																setFormData({
																	...formData,
																	subjects_info: [
																		...formData?.subjects_info,
																		{ subject: "", grade: "", price: "" },
																	],
																})
															}
														/>
													)}
												{formData?.choose === "at_home" &&
													formData?.subjects_info?.length - 1 !== index && (
														<i
															className="la la-times-circle text-danger cursor-pointer mt-1"
															style={{ fontSize: "2.5rem" }}
															onClick={() =>
																setFormData({
																	...formData,
																	subjects_info:
																		formData?.subjects_info?.filter(
																			(_, ind) => ind !== index
																		),
																})
															}
														/>
													)}
											</div>
										</React.Fragment>
									);
								})}

							{formData?.choose === "at_center" && (
								<>
									<div className="col-lg-6 col-sm-6 mb-3">
										<label className="text-label">
											{Translate[lang]?.lessons_frequency_week}
										</label>
										<Select
											value={formData?.lessons_frequency_week}
											name="lessons_frequency_week"
											placeholder={Translate[lang]?.select}
											options={[]}
											onChange={(e) =>
												setFormData({ ...formData, lessons_frequency_week: e })
											}
										/>
									</div>
									<div className="col-lg-6 col-sm-6 mb-3"></div>
								</>
							)}

							{/* Program Type */}
							<div className="col-lg-6 col-sm-6 ">
								<label className="text-label">
									{Translate[lang]?.program_type}
								</label>
								<div
									className="d-flex align-items-center"
									style={{ gap: "16px", height: "40px" }}>
									<label className="mb-0 d-flex" style={{ gap: "8px" }}>
										<input
											type="radio"
											name="foundation"
											checked={formData?.program_type === "foundation"}
											onChange={() =>
												setFormData({ ...formData, program_type: "foundation" })
											}
										/>
										<span>{Translate[lang].foundation}</span>
									</label>
									<label className="mb-0 d-flex" style={{ gap: "8px" }}>
										<input
											type="radio"
											name="curriculum"
											checked={formData?.program_type === "curriculum"}
											onChange={() =>
												setFormData({ ...formData, program_type: "curriculum" })
											}
										/>
										<span>{Translate[lang].curriculum}</span>
									</label>
								</div>
							</div>
							<div className="col-lg-6 col-sm-6 mb-3"></div>

							{/* Buttons */}
							<div className="col-lg-6 col-sm-6 mt-4 mb-5">
								{view && (
									<Button
										variant="secondary"
										type="button"
										className="w-100 fs-14"
										onClick={() => navigate("/education")}>
										{Translate[lang]?.cancel}
									</Button>
								)}
								{!view && (
									<Button
										variant="primary"
										type="submit"
										className="w-100 fs-14"
										disabled={loading}>
										{Translate[lang]?.save}
									</Button>
								)}
							</div>
						</div>
					</AvForm>
				</Col>
			</Row>
		</>
	);
};

export default AddProgram;
