import { useEffect, useState } from "react";
import { Button, Col, Form, Modal, Row } from "react-bootstrap";
import { AvField, AvForm } from "availity-reactstrap-validation";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import { Translate } from "../../../Enums/Tranlate";
import Select from "react-select";
import JobTitleService from "../../../../services/JobTitleService";
import "../style.scss";

const AddJobTitleModal = ({
	addModal,
	setAddModal,
	item,
	setShouldUpdate,
	type,
}) => {
	const [formData, setFormData] = useState({
		name: "",
		color: "",
		description: "",
		isActive: true,
	});
	let colorsOptions = [
		{ label: "Orange", value: "ORANGE" },
		{ label: "Yellow", value: "YELLOW" },
		{ label: "Blue", value: "BLUE" },
		{ label: "Red", value: "RED" },
		{ label: "Green", value: "GREEN" },
		{ label: "Gray", value: "GRAY" },
		{ label: "Simon", value: "SIMON" },
	];
	const [isAdd, setIsAdd] = useState(false);
	const [loading, setLoading] = useState(false);
	const jobTitleService = new JobTitleService();
	const lang = useSelector((state) => state.auth.lang);

	useEffect(() => {
		if (Object.keys(item)?.length === 0) {
			setIsAdd(true);
		} else {
			setIsAdd(false);
			setFormData({
				id: item?.id,
				name: item?.name,
				description: item?.description,
				isActive: item?.isActive,
				color: colorsOptions?.find((res) => res?.value === item?.color),
			});
		}
	}, [item]);

	const submit = (e) => {
		e.preventDefault();
		let data = {
			name: formData?.name,
			description: formData?.description,
			isActive: formData?.isActive,
			color: formData?.color?.value,
		};

		setLoading(true);
		if (isAdd) {
			jobTitleService
				.create(data)
				?.then((res) => {
					if (res && res?.status === 201) {
						toast.success(res?.data?.message);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		} else {
			jobTitleService
				.update(formData?.id, data)
				?.then((res) => {
					if (res && res?.status === 200) {
						toast.success(res?.data?.message);
						setShouldUpdate((prev) => !prev);
						setAddModal();
					}
					setLoading(false);
				})
				.catch(() => setLoading(false));
		}
	};

	return (
		<Modal
			className={
				lang === "en" ? "en fade addDepartment" : "ar fade addDepartment"
			}
			style={{ textAlign: lang === "en" ? "left" : "right" }}
			show={addModal}
			onHide={() => {
				setAddModal();
			}}>
			<AvForm className="form-horizontal" onValidSubmit={submit}>
				<Modal.Body>
					<Row>
						<Col md={12} className="mb-2">
							<div className="d-flex justify-content-between">
								<p className="mb-0"></p>
								<Button
									variant=""
									className="close"
									style={{
										right: lang === "en" ? "0" : "auto",
										left: lang === "ar" ? "0" : "auto",
									}}
									onClick={() => {
										setAddModal();
									}}>
									<span style={{ fontSize: "22px" }}>&times;</span>
								</Button>
							</div>
						</Col>
						<Col md={12} className="mb-4">
							<div className="d-flex justify-content-between align-items-center">
								<p
									className="mb-0"
									style={{
										fontWeight: "600",
										color: "#333",
										fontSize: "20px",
									}}>
									{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
									{Translate[lang]?.job_title}
								</p>
								<Form.Check
									type="switch"
									id="custom-switch"
									checked={formData?.isActive}
									onChange={(e) =>
										setFormData({ ...formData, isActive: e.target.checked })
									}
								/>
							</div>
						</Col>
						<Col md={12}>
							<AvField
								label={Translate[lang]?.job_title}
								type="text"
								placeholder={Translate[lang]?.job_title}
								bsSize="lg"
								name="name"
								validate={{
									required: {
										value: true,
										errorMessage: Translate[lang].field_required,
									},
								}}
								value={formData.name}
								onChange={(e) =>
									setFormData({ ...formData, name: e.target.value })
								}
							/>
						</Col>
						<Col md={12}>
							<label className="d-block">{Translate[lang]?.description}</label>
							<textarea
								className="w-100 py-2 px-3"
								placeholder={Translate[lang]?.description}
								style={{
									border: "1px solid hsl(0, 0%, 80%)",
									borderRadius: "10px",
									minHeight: "100px",
									maxHeight: "100px",
								}}
								value={formData?.description}
								onChange={(e) =>
									setFormData({ ...formData, description: e.target.value })
								}
							/>
						</Col>
						<Col md={12} className="mb-3">
							<label className="text-label">
								{Translate[lang]?.organization_color}
							</label>
							<Select
								value={formData?.color}
								name="color"
								placeholder={Translate[lang]?.select}
								options={colorsOptions}
								onChange={(e) => setFormData({ ...formData, color: e })}
							/>
						</Col>
					</Row>
				</Modal.Body>
				<Modal.Footer>
					{/* <Button onClick={setAddModal} variant="danger light">
            {Translate[lang]?.close}
            </Button> */}
					<Button
						variant="primary"
						type="submit"
						className="w-100"
						disabled={loading}>
						{isAdd ? Translate[lang]?.add : Translate[lang]?.edit}{" "}
						{Translate[lang]?.job_title}
					</Button>
				</Modal.Footer>
			</AvForm>
		</Modal>
	);
};

export default AddJobTitleModal;
