import React, { Fragment, useEffect } from "react";
import { useState } from "react";
import { Row, Col, Card, Table, Button } from "react-bootstrap";
import { useSelector } from "react-redux";
import Loader from "../../common/Loader";
import NoData from "../../common/NoData";
import Pagination from "../../common/Pagination/Pagination";
import { Translate } from "../../Enums/Tranlate";
import CardItem from "./CardItem";
import AddPositionsModal from "./AddPositionsModal";
import "./style.scss";
import { AvField, AvForm } from "availity-reactstrap-validation";
import ContactService from "../../../services/ContactService";
import "react-phone-input-2/lib/bootstrap.css";
import PhoneInput from "react-phone-input-2";
import { toast } from "react-toastify";
import PositionsService from "../../../services/PositionsService";

const Contact = () => {
	const [data, setData] = useState([]);
	const [formData, setFormData] = useState({
		salesEmail: "",
		supportEmail: "",
		call_number: "",
		another_call_number: "",
		location: "",
		whatsapp_number: "",
		time_from: "",
		time_to: "",
		day_from: "",
		day_to: "",
	});
	const [addModal, setAddModal] = useState(false);
	const [item, setItem] = useState({});
	const [hasData, setHasData] = useState(null);
	const [type, setType] = useState("contact");
	const [loading, setLoading] = useState(false);
	const [shouldUpdate, setShouldUpdate] = useState(false);
	const lang = useSelector((state) => state.auth?.lang);
	const Auth = useSelector((state) => state.auth?.auth);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const contactService = new ContactService();
	const positionsService = new PositionsService();

	useEffect(() => {
		if (type === "contact") {
			contactService?.getList().then((res) => {
				if (res?.status) {
					let info = res?.data?.data;
					setFormData({
						salesEmail: info?.salesEmail,
						supportEmail: info?.supportEmail,
						call_number: info?.callNumber,
						location: info?.location,
						another_call_number: info?.anotherCallNumber || "",
						whatsapp_number: info?.whatsappNumber,
						day_from: info?.dayFrom,
						day_to: info?.dayTo,
						time_from: info?.timeFrom,
						time_to: info?.timeTo,
					});
				}
			});
		}
	}, [type]);

	const submit = (e) => {
		e.preventDefault();
		if (!formData?.call_number) {
			toast.error("Enter Call Number");
			return;
		}
		let data = {
			salesEmail: formData?.salesEmail,
			supportEmail: formData?.supportEmail,
			callNumber: formData?.call_number,
			location: formData?.location,
			whatsappNumber: formData?.whatsapp_number,
			dayFrom: formData?.day_from,
			dayTo: formData?.day_to,
			timeFrom: formData?.time_from,
			timeTo: formData?.time_to,
		};
		if (!!formData?.another_call_number)
			data["anotherCallNumber"] = formData?.another_call_number;

		contactService.create(data).then((res) => {
			if (res?.status) {
				toast.success(Translate[lang].added_successfully);
			}
		});
	};

	return (
		<div className="department">
			<div className="tabs-div">
				<p
					onClick={() => setType("contact")}
					className={type === "contact" ? "activeP" : ""}>
					{Translate[lang].contact}
				</p>
				<p
					onClick={() => setType("position")}
					className={type === "position" ? "activeP" : ""}>
					{Translate[lang].position}
				</p>
			</div>
			<div className="mb-4">
				<div className="search-card d-flex justify-content-between align-items-center">
					<div>
						<span
							style={{
								fontWeight: "600",
								textTransform: "capitalize",
								color: "#000",
							}}>
							{Translate[lang][type]}
						</span>
					</div>
					<div className="add-department">
						{type === "position" && (
							<Button
								variant="primary"
								className="me-2 h-75"
								onClick={() => {
									setItem({});
									setAddModal(true);
								}}>
								<i className="la la-plus mx-1"></i>
								{Translate[lang]?.add} {Translate[lang]?.position}
							</Button>
						)}
					</div>
				</div>
			</div>

			<Row>
				<Col lg={12}>
					<Card>
						{type === "contact" && (
							<Row className="add-organization">
								<Col md={10}>
									<AvForm className="form-horizontal" onValidSubmit={submit}>
										<Row>
											<Col md={6}>
												<AvField
													label={`${Translate[lang]?.sales_email}`}
													type="email"
													placeholder={Translate[lang]?.sales_email}
													bsSize="lg"
													name="sales_email"
													validate={{
														required: {
															value: true,
															errorMessage: Translate[lang].field_required,
														},
													}}
													value={formData?.salesEmail}
													onChange={(e) =>
														setFormData({
															...formData,
															salesEmail: e.target.value,
														})
													}
												/>
											</Col>
											<Col md={6}>
												<AvField
													label={`${Translate[lang]?.support_email}`}
													type="email"
													placeholder={Translate[lang]?.support_email}
													bsSize="lg"
													name="supportEmail"
													validate={{
														required: {
															value: true,
															errorMessage: Translate[lang].field_required,
														},
													}}
													value={formData?.supportEmail}
													onChange={(e) =>
														setFormData({
															...formData,
															supportEmail: e.target.value,
														})
													}
												/>
											</Col>
											<Col md={6} className="mb-3">
												<label>{Translate[lang].call_number}</label>
												<PhoneInput
													country={"kw"}
													enableSearch={true}
													placeholder="Enter Phone"
													value={formData?.call_number}
													onChange={(e) =>
														setFormData({ ...formData, call_number: e })
													}
												/>
											</Col>
											<Col md={6} className="mb-3">
												<label>
													{Translate[lang].another_call_number} (
													{Translate[lang].optional})
												</label>
												<PhoneInput
													country={"kw"}
													enableSearch={true}
													placeholder="Enter Phone"
													value={formData?.another_call_number}
													onChange={(e) =>
														setFormData({ ...formData, another_call_number: e })
													}
												/>
											</Col>
											<Col md={6}>
												<AvField
													label={`${Translate[lang]?.location}`}
													type="text"
													placeholder={Translate[lang]?.location}
													bsSize="lg"
													name="location"
													validate={{
														required: {
															value: true,
															errorMessage: Translate[lang].field_required,
														},
													}}
													value={formData?.location}
													onChange={(e) =>
														setFormData({
															...formData,
															location: e.target.value,
														})
													}
												/>
											</Col>
											<Col md={6} className="mb-3">
												<label>{Translate[lang].whatsapp}</label>
												<PhoneInput
													country={"kw"}
													enableSearch={true}
													placeholder="Enter Phone"
													value={formData?.whatsapp_number}
													onChange={(e) =>
														setFormData({ ...formData, whatsapp_number: e })
													}
												/>
											</Col>
											<Col md={3}>
												<AvField
													label={`${Translate[lang]?.start_day}`}
													type="text"
													placeholder={Translate[lang]?.start_day}
													bsSize="lg"
													name="day_from"
													validate={{
														required: {
															value: true,
															errorMessage: Translate[lang].field_required,
														},
													}}
													value={formData?.day_from}
													onChange={(e) =>
														setFormData({
															...formData,
															day_from: e.target.value,
														})
													}
												/>
											</Col>
											<Col md={3}>
												<AvField
													label={`${Translate[lang]?.end_day}`}
													type="text"
													placeholder={Translate[lang]?.end_day}
													bsSize="lg"
													name="day_to"
													validate={{
														required: {
															value: true,
															errorMessage: Translate[lang].field_required,
														},
													}}
													value={formData?.day_to}
													onChange={(e) =>
														setFormData({ ...formData, day_to: e.target.value })
													}
												/>
											</Col>
											<Col md={3}>
												<AvField
													label={`${Translate[lang]?.time_from}`}
													type="time"
													placeholder={Translate[lang]?.time_from}
													bsSize="lg"
													name="time_from"
													validate={{
														required: {
															value: true,
															errorMessage: Translate[lang].field_required,
														},
													}}
													value={formData?.time_from}
													onChange={(e) =>
														setFormData({
															...formData,
															time_from: e.target.value,
														})
													}
												/>
											</Col>
											<Col md={3}>
												<AvField
													label={`${Translate[lang]?.time_to}`}
													type="time"
													placeholder={Translate[lang]?.time_to}
													bsSize="lg"
													name="time_to"
													validate={{
														required: {
															value: true,
															errorMessage: Translate[lang].field_required,
														},
													}}
													value={formData?.time_to}
													onChange={(e) =>
														setFormData({
															...formData,
															time_to: e.target.value,
														})
													}
												/>
											</Col>
											<Col md={6}>
												<Button
													variant="primary"
													type="submit"
													className="w-100 mt-3"
													disabled={loading}>
													{Translate[lang]?.confirm}
												</Button>
											</Col>
										</Row>
									</AvForm>
								</Col>
							</Row>
						)}
						{type === "position" && (
							<div
								className={`${hasData === 0 ? "text-center" : "table-style"} `}>
								{loading && (
									<div style={{ height: "400px" }}>
										<Loader />
									</div>
								)}

								{hasData === 1 && !loading && (
									<Table responsive>
										<thead>
											<tr className="text-center">
												<th>
													<strong>{Translate[lang]?.position}</strong>
												</th>
												<th></th>
												<th></th>
												<th>
													<strong>{Translate[lang]?.action}</strong>
												</th>
											</tr>
										</thead>

										<tbody className="table-body">
											{data?.map((item, index) => {
												return (
													<CardItem
														index={index}
														key={index}
														item={item}
														setItem={setItem}
														setAddModal={setAddModal}
														setShouldUpdate={setShouldUpdate}
													/>
												);
											})}
										</tbody>
									</Table>
								)}
								{hasData === 0 && <NoData />}
								<Pagination
									setData={setData}
									service={positionsService}
									shouldUpdate={shouldUpdate}
									setHasData={setHasData}
									setLoading={setLoading}
								/>
							</div>
						)}
					</Card>
				</Col>
			</Row>

			{addModal && (
				<AddPositionsModal
					item={item}
					addModal={addModal}
					setShouldUpdate={setShouldUpdate}
					setAddModal={() => setAddModal(false)}
				/>
			)}
		</div>
	);
};

export default Contact;
