import { useState } from "react";
import { Badge } from "react-bootstrap";
import { useSelector } from "react-redux";
import DeleteModal from "../../../common/DeleteModal";
import { Translate } from "../../../Enums/Tranlate";
import editIcon from "../../../../images/icons/edit.svg";
import trashIcon from "../../../../images/icons/delete.svg";
import JobTitleService from "../../../../services/JobTitleService";

const CardItem = ({
	item,
	setItem,
	index,
	setAddModal,
	setViewModal,
	setShouldUpdate,
}) => {
	const [deleteModal, setDeleteModal] = useState(false);
	const Auth = useSelector((state) => state.auth?.auth);
	const lang = useSelector((state) => state.auth?.lang);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const jobTitleService = new JobTitleService();

	return (
		<tr key={index} className="text-center">
			<td>{item?.name}</td>
			<td>{item?.description}</td>
			<td
				className="d-flex align-items-center text-capitalize"
				style={{ gap: "8px" }}>
				<span
					style={{
						background: getBackground(item?.color?.toLowerCase()),
						height: "32px",
						width: "32px",
						borderRadius: "50%",
						display: "inline-block",
					}}></span>
				{item?.color}
			</td>
			<td>
				<Badge
					className="text-capitalize"
					variant={`${item.isActive ? "success" : "danger"} light`}>
					{item?.isActive ? Translate[lang].active : Translate[lang].deactive}
				</Badge>
			</td>
			<td>
				<div className="d-flex" style={{ gap: "20px" }}>
					<img
						onClick={() => {
							setItem(item);
							setAddModal(true);
						}}
						src={editIcon}
						alt="edit"
						width={24}
						className="cursor-pointer"
					/>
					<img
						onClick={() => setDeleteModal(true)}
						src={trashIcon}
						alt="trash"
						width={24}
						className="cursor-pointer"
					/>
				</div>
			</td>
			{deleteModal && (
				<DeleteModal
					open={deleteModal}
					titleMsg={item?.name}
					deletedItem={item}
					modelService={jobTitleService}
					onCloseModal={setDeleteModal}
					setShouldUpdate={setShouldUpdate}
				/>
			)}
		</tr>
	);
};
const getBackground = (type) => {
	if (type === "orange") {
		return "#FFCABA";
	} else if (type === "yellow") {
		return "#FFDEAD";
	} else if (type === "blue") {
		return "#ECF3FC";
	} else if (type === "red") {
		return "#FFE5EB";
	} else if (type === "green") {
		return "#EFFBF2";
	} else if (type === "gray") {
		return "#F7F7F7";
	} else {
		return "#A1FFF3";
	}
};
export default CardItem;
